import Typography from '@material-ui/core/Typography';

import '@hims/core/dist/index.css';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
	// root: {
	// 	backgroundColor: '#ffffff',
	// 	padding: '10px',
	// },
	titleHeader: {
		marginTop: '15px',
	},
	gridContainer:{
		padding: theme.spacing(1),
		paddingBottom: '50px'
	}
}))


interface SubmitProps {
	invalidFields: string[];
}

export const Submit: React.FC<ICreateProposal.IProps & SubmitProps> = (props: ICreateProposal.IProps & SubmitProps): JSX.Element => {
	const {classes, contractType} = props;
	const headerclasses = useStyles();

	return (
		<React.Fragment>
			<Grid container className={headerclasses.gridContainer}>
				<Grid item sm={12} className={headerclasses.titleHeader}>
					<Typography className={classes.sectionTitle}>
						Review Proposal
					</Typography>
					<Typography gutterBottom className={classes.subSectionTitle}>
						Please review the proposal before submitting.
					</Typography>
				</Grid>
			</Grid>

			<Details {...props} viewOnly submitMode={true} />
			{ contractType === 'benefit' ? 	<StandardBenefitPlan {...props} viewOnly submitMode={true} /> : 
											<StandardSMEPlan {...props} submitMode={true} />
			}
			
		</React.Fragment>
	);
};
