export const convertClientData = (data: any) => {	
	const branchAddresses = data.branches.reduce ((result, item) => {

		let branchtel: any = [];
		if(item.contact_numbers !== undefined && item.contact_numbers !== null){
			branchtel = item.contact_numbers.map((tel) => {
				return {
					telno: tel.no ? tel.no : '',
				};
			});
		}

		let branchCP: any = [];
		if(item.contact_persons !== undefined && item.contact_persons !== null){
			branchCP = item.contact_persons.map((cp) => {
				return {
					name: cp.name ? cp.name : '',
					number: cp.contact_no ? cp.contact_no : '',
					email: cp.email_address ? cp.email_address : '',
					designation: cp.designation ? cp.designation : '',
				};
			});
		}

		const address = {
			floor: item.floor ? item.floor : '',
			unit: item.unit ? item.unit : '',
			buildingNumber: item.bldg_no ? item.bldg_no : '',
			buildingName: item.bldg_name ? item.bldg_name : '',
			street: item.street ? item.street : '',
			barangay: item.brgy ? item.brgy : '',
			city: item.city ? item.city : '',
			region: item.region ? item.region : '',
			zipCode: item.zip_code ? item.zip_code : '',
			addrTelephoneNumber: branchtel,
			addrContactPerson: branchCP
		};

		result.push (address);
		
		return result;
	}, []);

	let contactPersons: any = [];
	if(data.main_office_address.contact_persons !== undefined && data.main_office_address.contact_persons !== null){
		contactPersons = data.main_office_address.contact_persons.reduce ((result, item) => {
			const person = {
				name: item.name ? item.name : '',
				number: item.contact_no ? item.contact_no : '',
				email: item.email_address ? item.email_address : '',
				designation: item.designation ? item.designation : '',
			};

			result.push (person);
			
			return result;
		}, []);
	}

	let telephoneNumbers: any = [];
	if(data.main_office_address.contact_numbers !== undefined && data.main_office_address.contact_numbers !== null){
		telephoneNumbers = data.main_office_address.contact_numbers.reduce ((result, item) => {
			const number = {
				telno: item.no ? item.no : '',
			};

			result.push (number);
			
			return result;
		}, []);
	}

	const inputBusinessData = {
		registeredCorporateName: data.registered_name ? data.registered_name : '',
		tradeBrandName: data.brand_name ? data.brand_name : '',
		corporateAccountNo: data.corporate_account_no ? data.corporate_account_no : '',
		secRegistrationNo: data.sec_reg_no ? data.sec_reg_no : '',
		corporateTin: data.tin_no ? data.tin_no : '',
		vatType: data.vat_type ? data.vat_type : '',
		eVatNo: data.evat_no ? data.evat_no : '',
		nameDisplayedInCard: data.name_on_card ? data.name_on_card : '',
		subCompany: false,
		headCompany: '',
		headCompanyName: data.head_company_name ? data.head_company_name : '',
		contracts: data.contracts && data.contracts !== undefined 
			&& data.contracts.length > 0 ? data.contracts : [],
		member_data_fields: data.member_data_fields && data.member_data_fields !== undefined 
			&& data.member_data_fields.length > 0 ? data.member_data_fields : [],
		uploadedLogo: data.files ? data.files.filter(file => file.field.includes('icon')) : []
	};

	const inputNatureData = {
		industry: data.industry ? data.industry : '',
		ownership: data.ownership ? data.ownership : '',
		yearsOfExistence: data.years_of_existence ? data.years_of_existence : 0,
		sourcesOfFunds: data.sources_of_funds ? data.sources_of_funds : '',
	};

	const inputContactDetailsData = {
		floor: data.main_office_address.floor ? data.main_office_address.floor : '',
		unit: data.main_office_address.unit ? data.main_office_address.unit : '',
		buildingNumber: data.main_office_address.bldg_no ? data.main_office_address.bldg_no : '',
		buildingName: data.main_office_address.bldg_name ? data.main_office_address.bldg_name : '',
		street: data.main_office_address.street ? data.main_office_address.street : '',
		barangay: data.main_office_address.brgy ? data.main_office_address.brgy : '',
		city: data.main_office_address.city ? data.main_office_address.city : '',
		region: data.main_office_address.region ? data.main_office_address.region : '',
		zipCode: data.main_office_address.zip_code ? data.main_office_address.zip_code : '',
		authorizedSignatoryName: data.authorized_signatory ? data.authorized_signatory : '',
		authorizedSignatoryNumber: data.authorized_signatory_contact_no ? data.authorized_signatory_contact_no : '',
		authorizedSignatoryEmail: data.authorized_signatory_email_address ? data.authorized_signatory_email_address : '',
		authorizedSignatoryDesignation: data.authorized_signatory_designation ? data.authorized_signatory_designation : '',

		telephoneNumber: telephoneNumbers,
		contactPerson: contactPersons,
		branchAddress: branchAddresses,
	};

	const inputHMOData = {
		previousHMOProvider: data.prev_hmo_provider ? data.prev_hmo_provider : '',
		effectivityDate: data.prev_hmo_effectivity_date ? data.prev_hmo_effectivity_date : '',
		expiryDate: data.prev_hmo_expiry_date ? data.prev_hmo_expiry_date : '',
		uploadedUtilization: data.files ? data.files.filter(file => file.field.includes('utilization')) : [],
	};

	if (data.sales_channel && data.sales_channel !== undefined) {
		inputHMOData['salesChannel'] = data.sales_channel.type ? data.sales_channel.type : '';

		inputHMOData['agentName'] = data.sales_channel.agent_name ? data.sales_channel.agent_name : '';
		inputHMOData['brokerCompanyName'] = data.sales_channel.company_name ? data.sales_channel.company_name : '';
		inputHMOData['brokerContactPerson'] = data.sales_channel.contact_person ? data.sales_channel.contact_person : ''; 
		inputHMOData['brokerPosition'] = data.sales_channel.position ? data.sales_channel.position : '';
		inputHMOData['brokerContactNumber'] = data.sales_channel.contact_no ? data.sales_channel.contact_no : '';
		inputHMOData['brokerEmailAddress'] = data.sales_channel.email_address ? data.sales_channel.email_address : '';

		inputHMOData['bdoName'] = data.sales_channel.bdo_name ? data.sales_channel.bdo_name : '';
	}

	let client = {};

	client['businessData'] = inputBusinessData;
	client['natureData'] = inputNatureData;
	client['contactDetailsData'] = inputContactDetailsData;
	client['HMOData'] = inputHMOData;

	return client;
}

const filterSurehealthNodes = (nodesParam: any[]) => {
	let nodes = [...nodesParam];

	let firstSurehealth = false;

	nodes = nodes.map ((item: any) => {
		if (item.level === 6 && item.tree_path[3] === 'RI' && item.tree_path[4] === 'SH' ) {
			if (!firstSurehealth) {
				item['included'] = true;
				firstSurehealth = true;
			} else {
				item['included'] = false;
			}
		}

		return item;
	});

	return nodes;
}

export const convertNodeData = (nodesParam: any[], loadExisting = false) => {
	let nodeIDMap: any[] = [];

	let nodes = [...nodesParam];

	if (!loadExisting) {
		nodes = nodes.filter ((item: any) => {
			if (item.level === 1 || 
				item.level === 2 || 
				(item.level > 2 && item.tree_path[2] === 'BASE')
			) {
				return true;
			} 
			return false;
		});
	}

	nodes = nodes.sort ((node1: any, node2: any) => {
		if (node1.level === 3 && node1.code === 'BASE') return -1;
		if (node2.level === 3 && node2.code === 'BASE') return 1;

		if (node1.sort_index > node2.sort_index) return -1;
		if (node1.sort_index < node2.sort_index) return 1;

		if (node1.name > node2.name) return 1;
		if (node1.name < node2.name) return -1;

		return 0;
	});

	nodes = nodes.map ((item: any, i: number) => {
		const oldID = item['_id'];
		let itemPlanType = 'BASE';

		if (item.level > 2 && item.tree_path[2] !== 'BASE') {
			itemPlanType = item.tree_path[2];
		}

		const newID = "NODE-" + itemPlanType + "-"+i.toString().padStart(3, '0');

		item['included'] = true;

		const IDSet = {
			id: oldID,
			newID: newID
		};

		if (item['custom_metadata']['included_facilities'] && item['custom_metadata']['included_facilities'].length > 0) {
			const newIncludedFacilities = [...item['custom_metadata']['included_facilities']];

			item['custom_metadata']['partner_network_list'] = newIncludedFacilities.map((item: any, i: number) => {
				let newPartner = Object.assign({}, item);

				newPartner['parent'] = newID;
				newPartner['included'] = true;
				newPartner['index'] = i;

				return newPartner;
			});

			delete item.custom_metadata.included_facilities;
		}

		if (item['custom_metadata']['excluded_facilties'] && item['custom_metadata']['excluded_facilties'].length > 0) {
			let newExcludedFacilities = [...item['custom_metadata']['excluded_facilties']];

			let partnerNetworkList: any[] = [];

			if (item['custom_metadata']['partner_network_list'] !== undefined) {
				partnerNetworkList = [...item['custom_metadata']['partner_network_list']];
			}

			newExcludedFacilities = newExcludedFacilities.map((item: any, i: number) => {
				let newPartner = Object.assign({}, item);

				newPartner['parent'] = newID;
				newPartner['included'] = false;
				newPartner['index'] = i;

				return newPartner;
			});

			item['custom_metadata']['partner_network_list'] = [...partnerNetworkList, ...newExcludedFacilities];

			delete item.custom_metadata.excluded_facilties;
		}

		nodeIDMap.push (IDSet);

		if (loadExisting) {
			item['original_id'] = item['_id'];
		}

		item['id'] = newID;

		delete item['_id'];

		return Object.assign({}, item);
	});

	nodes = nodes.map ((item: any) => {
		if (loadExisting) {
			item['original_children'] = [...item['children']];
		}

		if (item['children'].length > 0) {
			item['children'] = item['children'].reduce ((result, id) => {
				const found = nodeIDMap.find ((set) => { return set.id === id; });

				if (found) {
					result.push(found.newID)
				}

				return result;
			}, []);
		}
		return item;
	});

	return nodes;
}

export const createExclusionList = (nodes: any[]) => {
	const eList = nodes.reduce((exResult, item: any) => {
		// if (item['custom_metadata']['is_excluded'] && item['custom_metadata']['is_excluded'] === true) {
		if (item['level'] === 6 && item['tree_path'][3] === 'EX') {
			const exTree = GlobalFunction.getNodeTree(item, nodes);
			exResult.push(exTree);
		}
		return exResult;
	}, []);

	return eList;
}

export const generatePlanNodes = (baseNodes: any[], name: string, code: string, tmp_data: any, basePlanType: string = 'BASE') => {
	let planNodes = [...baseNodes];
	let planTypeID = '';

	planNodes = planNodes.filter((item: any) => {
		return item.level > 2 && item.tree_path[2] === basePlanType
	});

	let nodeIDMap: any[] = [];

	planNodes = planNodes.map ((item: any, i: number) => {
		let newItem = Object.assign({}, item);

		const oldID = newItem['id'];
		const newID = "NODE-" + code + "-"+i.toString().padStart(3, '0')

		if ((newItem.level > 2 && newItem.tree_path[2] === basePlanType)
		) {
			newItem.tree_path = Object.assign({}, newItem.tree_path);
			newItem.tree_path[2] = code;
		} 

		if ((newItem.level === 3 && newItem.code === basePlanType)) {
			newItem['name'] = name;
			newItem['code'] = code;
			if (!newItem['custom_metadata']) {
				newItem['custom_metadata'] = {};
			}
			if (!newItem['custom_metadata_schema']) {
				newItem['custom_metadata_schema'] = {};
			}
			newItem['custom_metadata'] = Object.assign({}, newItem.custom_metadata);
			newItem['description'] = 'Specific Plan Type Template';
			newItem['sort_index'] = 2;
			newItem['custom_metadata']['title'] = "Benefits for " + name;
			newItem["custom_metadata"]["requested_rate"] = tmp_data['requestedRate'] ? tmp_data['requestedRate'] : 0;
			newItem["custom_metadata"]["indicative_principals"] = tmp_data['indicativeNumOfPrincipals'] ? tmp_data['indicativeNumOfPrincipals'] : 0;
			newItem["custom_metadata"]["indicative_dependents"] = tmp_data['indicativeNumOfDependents'] ? tmp_data['indicativeNumOfDependents'] : 0;
			newItem['custom_metadata_schema'] = {
				"properties":{
					"title": {
						"type": "string"
					},
					"requested_rate": {
						"type": "number"
					},
					"indicative_principals": {
						"type": "number"
					},
					"indicative_dependents": {
						"type": "number"
					}
				}
			};
			
			planTypeID = newID;
		}

		const IDSet = {
			id: oldID,
			newID: newID
		};

		nodeIDMap.push (IDSet);
		newItem['id'] = newID;
		newItem['edited'] = false;

		delete newItem.original_children;
		delete newItem.original_id;

		return newItem;
	});

	planNodes = planNodes.map ((item: any) => {
		if (item['children'].length > 0) {
			item['children'] = item['children'].reduce ((result, id) => {
				const found = nodeIDMap.find ((set) => { return set.id === id; });

				if (found) {
					result.push (found.newID);
				}
				return result;
			}, []);
		}

		if (item['custom_metadata'] && item['custom_metadata']['partner_network_list']) {
			let metadata = Object.assign({}, item['custom_metadata']);
			// let newPartnerNetworkList = [...item['custom_metadata']['partner_network_list']];

			// newPartnerNetworkList = newPartnerNetworkList.map((partner: any) => {
			// 	let newPartner = Object.assign({}, partner);

			// 	newPartner['parent'] = item['id'];

			// 	return newPartner;
			// });

			metadata['partner_network_list'] = [];

			item['custom_metadata'] = metadata;
		}

		if (item['original_node']) {
			const found = nodeIDMap.find ((set) => { return set.id === item['original_node']; });
				
			if (found) {
				item['original_node'] = found.newID;
			}
		}
		return item;
	});

	return {planNodes, planTypeID};
}

export const combineBaseBenefitNodes = (baseNodes: any[], benefitNodes: any[], addFlag = false) => {
	// Get all the plan types
	const planTypeNodes = benefitNodes.reduce((result, node) => {
		if (node.level === 3 && node.code !== "BASE") {
			result.push(node);
		}
		return result;
	}, []);

	let nodes = [...baseNodes];
	
	// remove the base eligiblity nodes
	nodes = nodes.filter ((item: any) => {
		return !([7,8].includes(item.level) && item.tree_path[3] === 'TC' && item.tree_path[4] === 'EL');
	});

	nodes = nodes.map ((item: any) => {
		if (item.level === 6 &&  item.tree_path[3] === 'TC' && item.tree_path[4] === 'EL') {
			let newItem = Object.assign({}, item);

			newItem.children = newItem.children.filter(child =>
	          nodes.find((node) => node.id === child) ? true : false
	        );
	        return newItem;
	    } else {
	    	return item;
	    }
	});

	// Duplicate the base nodes for every non-base plan type
	nodes = planTypeNodes.reduce ((result, planTypeNode) => {
		const {planNodes, planTypeID} = generatePlanNodes(result, planTypeNode.name, planTypeNode.code, planTypeNode);

		const index = result.findIndex(item => 
			item.level === 2 && 
			item.code === 'CT'
		);

		result[index]['children'].push(planTypeID);

		return [...result, ...planNodes];
	}, nodes);

	// Overlay the benefit nodes array to the base nodes array and return
	nodes = nodes.map ((node: any) => {
		let newNode = Object.assign({}, node);

		const found = benefitNodes.find((benefit: any) => {
			if (newNode.level === 8) {
				return 	newNode.name === benefit.name &&
			            newNode.code === benefit.code &&
			            newNode.level === benefit.level && 
			            newNode.tree_path[2] === benefit.tree_path[2] &&
			            newNode.tree_path[3] === benefit.tree_path[3] &&
			            newNode.tree_path[4] === benefit.tree_path[4] &&
			            newNode.tree_path[5] === benefit.tree_path[5] &&
			            newNode.tree_path[6] === benefit.tree_path[6] &&
			            newNode.tree_path[7] === benefit.tree_path[7];
			} else if (newNode.level === 7) {
				return 	newNode.name === benefit.name &&
			            newNode.code === benefit.code &&
			            newNode.level === benefit.level && 
			            newNode.tree_path[2] === benefit.tree_path[2] &&
			            newNode.tree_path[3] === benefit.tree_path[3] &&
			            newNode.tree_path[4] === benefit.tree_path[4] &&
			            newNode.tree_path[5] === benefit.tree_path[5] &&
			            newNode.tree_path[6] === benefit.tree_path[6];
			} else if (newNode.level === 6) {
				return 	newNode.name === benefit.name &&
			            newNode.code === benefit.code &&
			            newNode.level === benefit.level && 
			            newNode.tree_path[2] === benefit.tree_path[2] &&
			            newNode.tree_path[3] === benefit.tree_path[3] &&
			            newNode.tree_path[4] === benefit.tree_path[4] &&
			            newNode.tree_path[5] === benefit.tree_path[5];
			} else if (newNode.level === 5) {
				return 	newNode.name === benefit.name &&
			            newNode.code === benefit.code &&
			            newNode.level === benefit.level && 
			            newNode.tree_path[2] === benefit.tree_path[2] &&
			            newNode.tree_path[3] === benefit.tree_path[3] &&
			            newNode.tree_path[4] === benefit.tree_path[4];
			} else if (newNode.level === 4) {
				return 	newNode.name === benefit.name &&
			            newNode.code === benefit.code &&
			            newNode.level === benefit.level && 
			            newNode.tree_path[2] === benefit.tree_path[2] &&
			            newNode.tree_path[3] === benefit.tree_path[3];
			} else if (newNode.level === 3) {
				return 	newNode.name === benefit.name &&
			            newNode.code === benefit.code &&
			            newNode.level === benefit.level && 
			            newNode.tree_path[2] === benefit.tree_path[2];
			} else {
				return 	newNode.name === benefit.name &&
			            newNode.code === benefit.code &&
			            newNode.level === benefit.level;
			} 
		});

		if (found) {
			const foundNode = Object.assign({}, found);
			delete foundNode.id;
			delete foundNode.children;
			delete foundNode.node_path;
			delete foundNode.tree_id;
			delete foundNode.tree_path;

			newNode = Object.assign({}, newNode, foundNode);

			if (addFlag && newNode.level > 3 && newNode.tree_path[2] !== 'BASE') {
				newNode['edited'] = true;
			}
		} else {
			if (newNode.level > 6 || 
				newNode.level === 5 && newNode.tree_path[3] === 'RB' ||
				newNode.level === 5 && newNode.tree_path[3] === 'ID' ||
				newNode.level === 6 && newNode.tree_path[3] === 'EX' ||
				newNode.level === 6 && newNode.tree_path[4] === 'EM' ||
				newNode.level === 6 && newNode.tree_path[4] === 'CC' ||
				newNode.level === 6 && newNode.tree_path[4] === 'EC' ||
				newNode.level === 6 && newNode.tree_path[4] === 'DNT' || 
				newNode.level === 6 && newNode.tree_path[4] === 'ADD' ||
				newNode.level === 6 && newNode.tree_path[4] === 'SH' ||
				newNode.level === 6 && newNode.tree_path[4] === 'SD' ||
				newNode.level === 6 && newNode.tree_path[4] === 'PV' ) {

				newNode['included'] = false;
			}	
		}	

		return newNode;
	});

	if (addFlag) {
		nodes.map ((node: any, i: number) => {
			if (node.level > 3 && node.tree_path[2] !== 'BASE' && node.tree_path[3] !== 'RB') {
				const baseIndex = GlobalFunction.getMatchByPlanCode(nodes, nodes[i], 'BASE');

				if (baseIndex > -1 && nodes[baseIndex]['included'] && nodes[baseIndex]['included'] === true) {
					nodes[i]['included'] = true;

					if (!nodes[i]['edited']) {
						nodes[i]['is_base'] = true;
					}
				}
			}
		});
	}

	let count = 0;
	// Add back the eligiblity nodes

	// let exclusionNodes = benefitNodes.filter ((benefit: any) => {
	// 	return [5,6].includes(benefit.level) && !['SX','EF','TP'].includes(benefit.tree_path[4]) && benefit.tree_path[3] === 'EX';
	// });

	// exclusionNodes =  exclusionNodes.sort ((node1: any, node2: any) => {
	// 	if (node1.level < node2.level) return -1;
	// 	if (node1.level > node2.level) return 1;

	// 	return 0;
	// });

	let eligiblityNodes = benefitNodes.filter ((benefit: any) => {
		return [7,8].includes(benefit.level) && benefit.tree_path[3] === 'TC' && benefit.tree_path[4] === 'EL';
	});

	eligiblityNodes =  eligiblityNodes.sort ((node1: any, node2: any) => {
		if (node1.level < node2.level) return -1;
		if (node1.level > node2.level) return 1;

		return 0;
	});

	// [...exclusionNodes, ...eligiblityNodes].map ((benefit: any) => {
	eligiblityNodes.map ((benefit: any) => {
		// console.log (benefit);
		const found = nodes.find((node: any) => {
			if (benefit.level === 5) {
				return 	benefit.name === node.name &&
			            benefit.code === node.code &&
			            benefit.level === node.level && 
			            benefit.tree_path[2] === node.tree_path[2] &&
			            benefit.tree_path[3] === node.tree_path[3] &&
			            benefit.tree_path[4] === node.tree_path[4];
			} else if (benefit.level === 6) {
				return 	benefit.name === node.name &&
			            benefit.code === node.code &&
			            benefit.level === node.level && 
			            benefit.tree_path[2] === node.tree_path[2] &&
			            benefit.tree_path[3] === node.tree_path[3] &&
			            benefit.tree_path[4] === node.tree_path[4] &&
			            benefit.tree_path[5] === node.tree_path[5];
			} else if (benefit.level === 7) {
				return 	benefit.name === node.name &&
			            benefit.code === node.code &&
			            benefit.level === node.level && 
			            benefit.tree_path[2] === node.tree_path[2] &&
			            benefit.tree_path[3] === node.tree_path[3] &&
			            benefit.tree_path[4] === node.tree_path[4] &&
			            benefit.tree_path[5] === node.tree_path[5] &&
			            benefit.tree_path[6] === node.tree_path[6];
			} else if (benefit.level === 8) {
				return 	benefit.name === node.name &&
			            benefit.code === node.code &&
			            benefit.level === node.level && 
			            benefit.tree_path[2] === node.tree_path[2] &&
			            benefit.tree_path[3] === node.tree_path[3] &&
			            benefit.tree_path[4] === node.tree_path[4] &&
			            benefit.tree_path[5] === node.tree_path[5] &&
			            benefit.tree_path[6] === node.tree_path[6] &&
			            benefit.tree_path[7] === node.tree_path[7];
			}
			return false;
		});

		if (!found) {
	    	const newID = "NODE-LOAD-"+(count).toString().padStart(3, '0');
	    	const parentLevel = benefit.level - 1;
	    	const parentCode = benefit.tree_path[benefit.level-2];
	    	const parentIndex = nodes.findIndex ((node) => {
	    		if (benefit.level === 5) {
	    			return 	parentCode === node.code &&
			            parentLevel === node.level && 
			            benefit.tree_path[2] === node.tree_path[2] &&
			            benefit.tree_path[3] === node.tree_path[3];
	    		} else if (benefit.level === 6) {
	    			return 	parentCode === node.code &&
			            parentLevel === node.level && 
			            benefit.tree_path[2] === node.tree_path[2] &&
			            benefit.tree_path[3] === node.tree_path[3] &&
			            benefit.tree_path[4] === node.tree_path[4];
	    		} else if (benefit.level === 7) {
	    			return 	parentCode === node.code &&
			            parentLevel === node.level && 
			            benefit.tree_path[2] === node.tree_path[2] &&
			            benefit.tree_path[3] === node.tree_path[3] &&
			            benefit.tree_path[4] === node.tree_path[4] &&
			            benefit.tree_path[5] === node.tree_path[5];
	    		} else if (benefit.level === 8) {
	    			return 	parentCode === node.code &&
			            parentLevel === node.level && 
			            benefit.tree_path[2] === node.tree_path[2] &&
			            benefit.tree_path[3] === node.tree_path[3] &&
			            benefit.tree_path[4] === node.tree_path[4] &&
			            benefit.tree_path[5] === node.tree_path[5] &&
			            benefit.tree_path[6] === node.tree_path[6];
	    		}
	    		return false;
	    	});

			let newChildren = [...nodes[parentIndex].children];
			newChildren.push(newID);

			nodes[parentIndex]['children'] = newChildren;

			let newNode = Object.assign({}, benefit);
			newNode['included'] = true;
			newNode['id'] = newID;

			nodes.push(newNode);
			count++;
		}
	
	});

	return nodes;
}

export const getInvalidFields = (cData: any,pInfo:any): any => {
	let fields: string[] = [];
	let wrongFormat: string[] = [];
	let wrongFormatIds: string[] = [];
	let wrongFormatNo: string[] = [];
	let wrongFormatNoIds: string[] = [];
	
	if(cData.principalData.numOfPrincipals === ''){
		fields.push("No. of Principals");
	}
	if(cData.principalData.numOfDependents === ''){
		fields.push("No. of Dependents");
	}
	if((Number(cData.principalData.maleDependents)!==0 || Number(cData.principalData.femaleDependents) !== 0) && 
	(Number(cData.principalData.maleDependents)+Number(cData.principalData.femaleDependents)) !== Number(cData.principalData.numOfDependents))
	{
		fields.push("Male and female dependents do not match the total");
	}
	if((Number(cData.principalData.malePrincipals)!==0 || Number(cData.principalData.femalePrincipals) !== 0) &&
	(Number(cData.principalData.malePrincipals)+Number(cData.principalData.femalePrincipals)) !== Number(cData.principalData.numOfPrincipals))
	{
		fields.push("Male and female principals do not match the total");
	}
	// Registered Corporate Name
	if (cData.businessData.registeredCorporateName === '') {
		fields.push("Registered Corporate Name");
	}

	// Trade / Brand Name
	if (cData.businessData.tradeBrandName === '') {
		fields.push("Trade / Brand Name");
	}

	// Corporate Account No.
	if (cData.businessData.corporateAccountNo !== undefined && cData.businessData.corporateAccountNo !== '') {
		const corp_account_no = cData.businessData.corporateAccountNo;
		if(!corp_account_no.match(/\d{2}-\d{2}-\d{5}$/)) {
			if(!wrongFormatNo.includes("Corporate Account No.")){
				wrongFormatNo.push("Corporate Account No.");
			}
			if(!wrongFormatNoIds.includes("corporateAccountNo")){
				wrongFormatNoIds.push("corporateAccountNo");
			}
		}
	}

	// SEC Registration No.
	if (cData.businessData.secRegistrationNo === '') {
		fields.push("SEC Registration No.");
	}

	// Corporate TIN
	if (cData.businessData.corporateTin === '') {
		fields.push("Corporate TIN");
	}

	// VAT Type
	if (cData.businessData.vatType === '') {
		fields.push("VAT Type");
	}

	// E-VAT No.
	// if (cData.businessData.eVatNo === '') {
	// 	fields.push("E-VAT No.");
	// }

	// Name Displayed In Card
	if (cData.businessData.nameDisplayedInCard === '') {
		fields.push("Name Displayed In Card");
	}

	// Industry
	if (cData.natureData.industry === '') {
		fields.push("Industry");
	}

	// Ownership
	if (cData.natureData.ownership === '') {
		fields.push("Ownership");
	}

	// Floor
	if (cData.contactDetailsData.floor === '') {
		fields.push("Main Office Address - Floor");
	}

	// // Unit
	// if (cData.contactDetailsData.unit === '') {
	// 	fields.push("Main Office Address - Unit");
	// }

	// Building Number
	if (cData.contactDetailsData.buildingNumber === '') {
		fields.push("Main Office Address - Building Number");
	}

	// Building Name
	if (cData.contactDetailsData.buildingName === '') {
		fields.push("Main Office Address - Building Name");
	}

	// Street
	if (cData.contactDetailsData.street === '') {
		fields.push("Main Office Address - Street");
	}

	// Barangay
	if (cData.contactDetailsData.barangay === '') {
		fields.push("Main Office Address - Barangay");
	}

	// City
	if (cData.contactDetailsData.city === '') {
		fields.push("Main Office Address - City");
	}

	// Region
	if (cData.contactDetailsData.region === '') {
		fields.push("Main Office Address - Region");
	}

	// Zip Code
	if (cData.contactDetailsData.zipCode === '') {
		fields.push("Main Office Address - Zip Code");
	}

	if(pInfo.coverage_start_date === '' || pInfo.coverage_end_date === '')
	{
		fields.push('Proposal Coverage Period');
	}

	/*if(cData.HMOData.effectivityDate === '' || cData.HMOData.expiryDate === ''){
		fields.push("Effectivity Date Required");
	}*/

	// Branches
	cData.contactDetailsData.branchAddress.map((branch: any, parentIndex: number) => {
		if (branch['floor'] === '') {
			if (!fields.includes("Branch Office Address - Floor")) {
				fields.push("Branch Office Address - Floor");
			}
		}
		// if (branch['unit'] === '') {
		// 	if (!fields.includes("Branch Office Address - Unit")) {
		// 		fields.push("Branch Office Address - Unit");
		// 	}
		// }
		if (branch['buildingNumber'] === '') {
			if (!fields.includes("Branch Office Address - Building Number")) {
				fields.push("Branch Office Address - Building Number");
			}
		}
		if (branch['buildingName'] === '') {
			if (!fields.includes("Branch Office Address - Building Name")) {
				fields.push("Branch Office Address - Building Name");
			}
		}
		if (branch['street'] === '') {
			if (!fields.includes("Branch Office Address - Street")) {
				fields.push("Branch Office Address - Street");
			}
		}
		if (branch['barangay'] === '') {
			if (!fields.includes("Branch Office Address - Barangay")) {
				fields.push("Branch Office Address - Barangay");
			}
		}
		if (branch['city'] === '') {
			if (!fields.includes("Branch Office Address - City")) {
				fields.push("Branch Office Address - City");
			}
		}
		if (branch['region'] === '') {
			if (!fields.includes("Branch Office Address - Region")) {
				fields.push("Branch Office Address - Region");
			}
		}
		if (branch['zipCode'] === '') {
			if (!fields.includes("Branch Office Address - Zip Code")) {
				fields.push("Branch Office Address - Zip Code");
			}
		}

		branch['addrTelephoneNumber'].map((tel) => {
			if (tel['telno'] === '') {
				if (!fields.includes("Branch Telephone Number/Trunk Line")) {
					fields.push("Branch Telephone Number/Trunk Line");
				}
			}
		});	

		branch['addrContactPerson'].map((person: any, idx:number) => {
			if (person['name'] === '') {
				if (!fields.includes("Branch Contact Person - Name")) {
					fields.push("Branch Contact Person - Name");
				}
			}
			if (person['number'] === '') {
				if (!fields.includes("Branch Contact Person - Contact Number")) {
					fields.push("Branch Contact Person - Contact Number");
				}
			}
			if (person['email'] === '') {
				if (!fields.includes("Branch Contact Person - Email")) {
					fields.push("Branch Contact Person - Email");
				}
			}else if (!person['email'].match(/\S+@\S+\.\S+/) 
				|| person['email'].indexOf(' ') !== -1
				|| person['email'].indexOf('..') !== -1){
				if (!wrongFormat.includes("Branch Contact Person - Email")) {
					wrongFormat.push("Branch Contact Person - Email")
				}
				if (!wrongFormatIds.includes("addrEmail-"+parentIndex+"-"+idx)) {
					wrongFormatIds.push("addrEmail-"+parentIndex+"-"+idx)
				}
			}
			if (person['designation'] === '') {
				if (!fields.includes("Branch Contact Person - Designation")) {
					fields.push("Branch Contact Person - Designation");
				}
			}
		});	
	});	

	// Telephone Number/Trunk Line
	cData.contactDetailsData.telephoneNumber.map((tel) => {
		if (tel['telno'] === '') {
			if (!fields.includes("Telephone Number/Trunk Line")) {
				fields.push("Telephone Number/Trunk Line");
			}
		}
	});	

	// Contact Persons
	if (cData.contactDetailsData.contactPerson.length === 0) {
		fields.push("Contact Persons");
	}

	cData.contactDetailsData.contactPerson.map((person: any, idx:number) => {
		if (person['name'] === '') {
			if (!fields.includes("Contact Person - Name")) {
				fields.push("Contact Person - Name");
			}
		}
		if (person['number'] === '') {
			if (!fields.includes("Contact Person - Contact Number")) {
				fields.push("Contact Person - Contact Number");
			}
		}
		if (person['email'] === '') {
			if (!fields.includes("Contact Person - Email")) {
				fields.push("Contact Person - Email");
			}
		}else if (!person['email'].match(/\S+@\S+\.\S+/) 
			|| person['email'].indexOf(' ') !== -1
			|| person['email'].indexOf('..') !== -1){
			if (!wrongFormat.includes("Contact Person - Email")) {
				wrongFormat.push("Contact Person - Email")
			}
			if (!wrongFormatIds.includes("email-"+idx)) {
				wrongFormatIds.push("email-"+idx)
			}
		}
		if (person['designation'] === '') {
			if (!fields.includes("Contact Person - Designation")) {
				fields.push("Contact Person - Designation");
			}
		}
	});	

	// Authorized Signatory
	if (cData.contactDetailsData.authorizedSignatoryName === '') {
		fields.push("Authorized Signatory");
	}

	// Authorized Email
	if (cData.contactDetailsData.authorizedSignatoryEmail !== undefined
		&& cData.contactDetailsData.authorizedSignatoryEmail !== '') {
		const email_sign = cData.contactDetailsData.authorizedSignatoryEmail;
		if(!email_sign.match(/\S+@\S+\.\S+/) 
			|| email_sign.indexOf(' ') !== -1
			|| email_sign.indexOf('..') !== -1){
			if(!wrongFormat.includes("Authorized Signatory Email")){
				wrongFormat.push("Authorized Signatory Email");
			}
			if(!wrongFormatIds.includes("authorizedSignatoryEmail")){
				wrongFormatIds.push("authorizedSignatoryEmail");
			}
		}
	}

	// // Contact Person 1

	// Previous HMO Provider
	if (cData.HMOData.previousHMOProvider === '') {
		fields.push("Previous HMO Provider");
	}

	// Agent / Broker

	if(cData.HMOData.salesChannel === ''){
		fields.push('Sales Channel');
	}

	if (cData.HMOData.salesChannel === 'agent') {
		if (cData.HMOData.agentName === '') {
			fields.push("Agent Name");
		}
	}

	if (cData.HMOData.salesChannel === 'broker') {
		if (cData.HMOData.brokerCompanyName === '') {
			fields.push("Broker - Name of Company");
		}
		if (cData.HMOData.brokerContactPerson === '') {
			fields.push("Broker - Contact Person");
		}
		if (cData.HMOData.brokerPosition === '') {
			fields.push("Broker - Position");
		}
		if (cData.HMOData.brokerContactNumber === '') {
			fields.push("Broker - Contact Number");
		}
		if (cData.HMOData.brokerEmailAddress === '') {
			fields.push("Broker - Email Address");
		}
	}

	if (cData.HMOData.salesChannel === 'bdo') {
		if (cData.HMOData.bdoName === '') {
			fields.push("BDO Name");
		}
	}

	return {
		fields: fields,
		wrong_format: wrongFormat,
		wrong_format_ids: wrongFormatIds,
		wrong_format_no: wrongFormatNo,
		wrong_format_no_ids: wrongFormatNoIds
	}
}

export const getClientPayload = (data: any): ClientPayloadType => {
	console.log("data to be sent: ", data)
	const branchAddresses = data.contactDetailsData.branchAddress.reduce ((result, item) => {
		
		const branchtel = item.addrTelephoneNumber.map((tel) => {
			return {
				no: tel.telno,
			};
		});

		const branchCP = item.addrContactPerson.map((cp) => {
			return {
				name: cp.name,
				contact_no: cp.number,
				email_address: cp.email,
				designation: cp.designation,
			};
		});

		const address = {
			floor: parseInt(item.floor),
			unit: item.unit,
			bldg_no: item.buildingNumber,
			bldg_name: item.buildingName,
			street: item.street,
			brgy: item.barangay,
			city: item.city,
			region: item.region,
			zip_code: item.zipCode,
			contact_numbers: branchtel,
			contact_persons: branchCP
		};

		result.push (address);
		
		return result;
	}, []);

	const contactPersons = data.contactDetailsData.contactPerson.reduce ((result, item) => {
		const person = {
			name: item.name,
			contact_no: item.number,
			email_address: item.email,
			designation: item.designation,
		};

		result.push (person);
		
		return result;
	}, []);

	const telephoneNumbers = data.contactDetailsData.telephoneNumber.reduce ((result, item) => {
		const number = {
			no: item.telno,
		};

		result.push (number);
		
		return result;
	}, []);

	const payload = {
		// Business
		registered_name: data.businessData.registeredCorporateName,
		brand_name: data.businessData.tradeBrandName,
		name_on_card: data.businessData.nameDisplayedInCard,
		tin_no: data.businessData.corporateTin,
		vat_type: data.businessData.vatType,
		evat_no: data.businessData.eVatNo,
		corporate_account_no: data.businessData.corporateAccountNo,
		sec_reg_no: data.businessData.secRegistrationNo,
		head_company_name: data.businessData.headCompanyName,

		// need to reference head company id
		// parent_id: data.businessData.headCompany,

		// need sub-companies
		// children: data.businessData.subCompany,

		// Nature
		industry: data.natureData.industry,
		ownership: data.natureData.ownership,
		years_of_existence: parseInt(data.natureData.yearsOfExistence),
		sources_of_funds: data.natureData.sourcesOfFunds,

		// Contact Details
		authorized_signatory: data.contactDetailsData.authorizedSignatoryName,
		authorized_signatory_contact_no: data.contactDetailsData.authorizedSignatoryNumber,
		authorized_signatory_email_address: data.contactDetailsData.authorizedSignatoryEmail,
		authorized_signatory_designation: data.contactDetailsData.authorizedSignatoryDesignation,
		main_office_address: {
			floor: parseInt(data.contactDetailsData.floor),
			unit: data.contactDetailsData.unit,
			bldg_no: data.contactDetailsData.buildingNumber,
			bldg_name: data.contactDetailsData.buildingName,
			street: data.contactDetailsData.street,
			brgy: data.contactDetailsData.barangay,
			city: data.contactDetailsData.city,
			region: data.contactDetailsData.region,
			zip_code: data.contactDetailsData.zipCode,
			contact_numbers: telephoneNumbers,
			contact_persons: contactPersons,
		}, 

		branches: branchAddresses,
		//contact_numbers: telephoneNumbers,
		//contact_persons: contactPersons,

		// HMO
		prev_hmo_provider: data.HMOData.previousHMOProvider,
		prev_hmo_effectivity_date: data.HMOData.effectivityDate,
		prev_hmo_expiry_date: data.HMOData.expiryDate,
		// Others
		status: 'Potential',
		// attachments: ,
		// arrangement: ,
		// member_data_fields: ,
	};
	if(data.businessData.contracts && data.businessData.contracts !== undefined 
		&& data.businessData.contracts.length > 0){
			payload["contracts"] = data.businessData.contracts;
	}
	
	if(data.businessData.member_data_fields && data.businessData.member_data_fields !== undefined 
		&& data.businessData.member_data_fields.length > 0){
			payload["member_data_fields"] = data.businessData.member_data_fields;
	}

	if (data.HMOData.salesChannel && data.HMOData.salesChannel !== '' && data.HMOData.salesChannel !== undefined) {
		payload['sales_channel'] = {
			type: data.HMOData.salesChannel
		};

		if (data.HMOData.salesChannel === 'agent') {
			payload['sales_channel']['agent_name'] = data.HMOData.agentName;
		} else if (data.HMOData.salesChannel === 'broker') {
			payload['sales_channel']['company_name'] = data.HMOData.brokerCompanyName;
			payload['sales_channel']['contact_person'] = data.HMOData.brokerContactPerson;
			payload['sales_channel']['position'] = data.HMOData.brokerPosition;
			payload['sales_channel']['contact_no'] = data.HMOData.brokerContactNumber;
			payload['sales_channel']['email_address'] = data.HMOData.brokerEmailAddress;
		} else if (data.HMOData.salesChannel === 'bdo') {
			payload['sales_channel']['bdo_name'] = data.HMOData.bdoName;
		}
	}

	return payload;
};



