const selectCategoryNode = createSelector(selectPlanPackageNodes, selectBenefitCategoryNodes, getSelectedPlanType, getSelectedPlanPackage, getSelectedBenefitCategory, (planPackageNodes, benefitCategoryNodes, selectedPlanType, selectedPlanPackage, selectedBenefitCategory) => {
  return getCategoryNode(planPackageNodes, benefitCategoryNodes, selectedPlanType, selectedPlanPackage, selectedBenefitCategory);
});

const selectSummaryPackages = createSelector(selectPlanPackageNodes, selectBenefitCategoryNodes, selectBenefitTypeNodes, selectMainBenefitNodes, (planPackageNodes, benefitCategoryNodes, benefitTypeNodes, mainBenefitNodes) => {
	let nodes = benefitCategoryNodes.filter((category: any) => {
		let benefits: any[] = [];

		if (["SH","DNT","EC","CC","PV", 'EM', "ADD", "SD"].includes(category.code)) {
			benefits = benefitTypeNodes.filter((benefit) => {
				return 	benefit.tree_path[2] === category.tree_path[2] &&
						benefit.tree_path[3] === category.tree_path[3] &&
						benefit.tree_path[4] === category.tree_path[4] &&
						benefit.included === true
			});

			benefits = benefits.filter((benefit) => {
				if (benefit['is_base']) {
					return false;
				}
				return true;
			});
		} else {
			benefits = mainBenefitNodes.filter((benefit) => {
				return 	benefit.tree_path[2] === category.tree_path[2] &&
						benefit.tree_path[3] === category.tree_path[3] &&
						benefit.tree_path[4] === category.tree_path[4] &&
						benefit.included === true
			});

			benefits = benefits.filter((benefit) => {
				if (benefit['is_base']) {
					return false;
				}
				return true;
			});
		}	

		return (
			category.tree_path[2] !== "BASE" &&
			(["CV", "RI"].includes(category.tree_path[3]) ) &&
			benefits.length > 0
		);
	});

	let RBnodes = planPackageNodes.filter((planPackage: any) => {
		let benefits: any[] = [];

		if (["RB"].includes(planPackage.code)) {
			benefits = benefitCategoryNodes.filter((benefit) => {
				return 	benefit.tree_path[2] === planPackage.tree_path[2] &&
						benefit.tree_path[3] === planPackage.tree_path[3] &&
						benefit.included === true
			});

			benefits = benefits.filter((benefit) => {
				if (benefit['is_base']) {
					return false;
				}
				return true;
			});
		}

		return (
			planPackage.tree_path[2] !== "BASE" &&
			benefits.length > 0
		);
	});

	nodes = [...nodes, ...RBnodes];

	// filter duplicates
	nodes = nodes.filter ((node: any, i: number)  => {
		const index = nodes.findIndex(category => 
		    category.name === node.name && 
		    category.code === node.code
		);

		if (index !== undefined && index !== i) {
			return false;
		}
		return true;
	});

	return nodes;
});

const selectSummaryBenefits = createSelector(selectPlanTypeNodes, selectBenefitCategoryNodes, selectBenefitTypeNodes, selectMainBenefitNodes, getSelectedSummaryPackage, getSelectedSummaryCategory, (planTypeNodes, benefitCategoryNodes, benefitTypeNodes, mainBenefitNodes, selectedSummaryPackage, selectedSummaryCategory) => {
	let nodes = planTypeNodes.reduce ((result, item) => {
		if (item.code !== "BASE") {
			let newPlanItem = Object.assign({}, item);
			const code = newPlanItem.code;

			let benefits: any[] = [];

			if (["SH","DNT","EC","CC","PV", 'EM', "ADD", "SD"].includes(selectedSummaryCategory)) {
				benefits = benefitTypeNodes.filter((item) => {
					return 	item.tree_path[2] === code &&
							item.tree_path[3] === selectedSummaryPackage &&
							item.tree_path[4] === selectedSummaryCategory &&
							item.included === true
				});

				benefits = benefits.filter((benefit) => {
					if (benefit['is_base']) {
						return false;
					}
					return true;
				});

			} else if (["RB"].includes(selectedSummaryCategory)) {
				benefits = benefitCategoryNodes.filter((item) => {
					return 	item.tree_path[2] === code &&
							item.tree_path[3] === selectedSummaryCategory &&
							item.included === true
				});

				let first = true;

				benefits = benefits.filter((benefit) => {
					if (benefit['is_base'] || !first) {
						return false;
					}
					first = false;
					return true;
				});
			} else {
				benefits = mainBenefitNodes.filter((item) => {
					return 	item.tree_path[2] === code &&
							item.tree_path[3] === selectedSummaryPackage &&
							item.tree_path[4] === selectedSummaryCategory &&
							item.included === true
				});

				benefits = benefits.filter((benefit) => {
					if (benefit['is_base']) {
						return false;
					}
					return true;
				});
			}

			newPlanItem['child_nodes'] = benefits;

			if (newPlanItem['child_nodes'].length > 0) {
				result.push(newPlanItem);
			}

		}

		return result;

	}, new Array());

	return nodes;
});


