import * as React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Dialog, DialogTitle, DialogContent,
    DialogContentText, DialogActions, Grid,
    IconButton, FormControlLabel, Checkbox, FormGroup} from '@material-ui/core/';
import CloseIcon from '@material-ui/icons/Close';

interface ProposalApprovalModalProps {
    isModalOpen: boolean;
    id: string;
    message: string;
    title: string;
    buttons?: any | null;
    allowRevision?: any | null;
    onClose: (flag: boolean) => void;
    allowedRevise: boolean;
    setAllowedRevise: (flag: boolean) => void;
}

const useStyles = makeStyles(theme => ({
    root: {
        flexWrap:'wrap'
    },
    dialog:{
        align:'center',
        justify:'center',
        width:'100%',
        fontFamily: 'usual',
    },
    dialogContent :{
        paddingTop : '0px',
    },
    dialogContentText :{
        fontSize: '0.875rem'
    },
    container: {
        paddingRight:'15px',
        paddingLeft:'15px',
        width: '450px'
    },
    dialogTitle: {
        marginTop: theme.spacing(2),
        marginBottom: theme.spacing(2),
        fontSize: '16px',
        color: '#272E4C',
        fontWeight: 700
    },
    dialogContentContainer: {
        width: '100%'
    },
    dialogAction: {
        marginBottom: theme.spacing(3),
        marginTop: theme.spacing(2),
        justifyContent:'center'
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    }
}));
  

export const ProposalApprovalModal: React.FC<ProposalApprovalModalProps> = (
	props: ProposalApprovalModalProps,
): JSX.Element => {
    const {
        isModalOpen,
        id
    } = props;
    const classes = useStyles();
    
    const onClose = () =>{
        if(props.onClose){
            props.onClose(false);
        }
    }

    return (
        <Dialog
            id={id}
            open={isModalOpen}
            aria-labelledby="modal-title"
            className={classes.dialog}
            style={{zIndex: 9999}}
        >
            <div>
                <Grid container className={classes.container} justify="flex-start" alignItems="flex-start" direction="column">
                    <Grid item xs={12}>
                        <DialogTitle className={classes.dialogTitle} disableTypography={true} id="modal-title" >
                            {props.title}
                            <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
                                <CloseIcon />
                            </IconButton>
                        </DialogTitle>
                    </Grid>
                    <Grid item xs={12} className={classes.dialogContentContainer}>
                        <DialogContent className={classes.dialogContent} style={{padding: '0px 20px 10px 20px'}}>
                            <DialogContentText className={classes.dialogContentText}>
                                {props.message}
                                {props.allowRevision === true ?
                                    <div>
                                        <FormGroup>
                                            <FormControlLabel
                                                style={{paddingTop: '15px'}}
                                                className={"allowed_revision"}
                                                control={<Checkbox
                                                    checked={props.allowedRevise}
                                                    color="primary"
                                                    onChange={e => {
                                                        e.stopPropagation();
                                                        console.log(" e.target.checked >>>", e.target.checked)
                                                        props.setAllowedRevise(e.target.checked)
                                                    }}
                                                />}
                                                label="Allow the BDO to revise"
                                            />
                                        </FormGroup>
                                    </div>: null
                                }
                            </DialogContentText>
                        </DialogContent>
                        <DialogActions className={classes.dialogAction}>
                            {props.buttons}
                        </DialogActions>
                    </Grid>
                </Grid>
            </div>
        </Dialog>
    )
}

ProposalApprovalModal.defaultProps = {
    isModalOpen: false,
    id: 'proposal_approval_modal',
    message: "This is a sample modal.",
    title: "Sample Modal",
    allowRevision: false
};
