import React, { useState, useRef } from 'react';
import Snackbar from  '@material-ui/core/Snackbar';
import SnackbarContent from  '@material-ui/core/SnackbarContent';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { ImageViewer } from '../ImageViewer';

import {
    faDownload,
    faFileUpload,
    faTimesCircle,
	faFilePdf,
	faFileWord
} from '@fortawesome/free-solid-svg-icons';

import {
	Grid, Popper, Typography,
	Paper, RadioGroup, Radio, Button,
    FormControlLabel, ClickAwayListener
} from '@material-ui/core';
import Dropzone from 'react-dropzone';

// import { IApproveProposal } from 'Pages/ApproveProposal/IApproveProposal';

interface FinalizeProps {
	classes: any;
	selectedFile: any;
	setDownloadFile: any;
	isApprover: boolean;
	setSelectedFile: any;
	setDeleteFile: any;
	uploaded: any;
	downloaded: any;
	setFileUpload: any;
	selectedFormat: string;
	setSelectedFormat: any;
	showDownloadScreen: any;
	setDownloadScreen: any;
	setLoading: any;
}
// export const Finalize:React.FC<IApproveProposal.IProps & FinalizeProps> = (props: IApproveProposal.IProps & FinalizeProps) => {
export const Finalize:React.FC<FinalizeProps> = (props: FinalizeProps) => {
	const { classes, uploaded, downloaded, selectedFile, isApprover,
		setSelectedFile, setDownloadFile, setDeleteFile, setFileUpload,
		selectedFormat, setSelectedFormat, setDownloadScreen, setLoading } = props;

	const [isPopoverOpen, setPopupoverOpen] = useState(false);
	const anchorRef = useRef(null);
	const [arrowRef, setArrowRef] = useState<any>(null);
    const [hasSignedProposal] = useState(true);
	const [imageViewerOpen, setImageViewerOpen] = useState(false);
	const [index, setIndex] = useState(0);
	// const [isLoading, setLoading] = useState(false);

	function handleClose(){
		console.log('setPopupoverOpen');
        setPopupoverOpen(false);
    }



    function handleOpenPopper(){
        setPopupoverOpen(true);
    }

    function handleOpenImageViewer(index:number){
    	setIndex(index);
    	setImageViewerOpen(true);
    }

	const show = (att:string) => {
		var rjms = document.getElementById('snkbrcont');
		if(rjms!=undefined)
			{rjms.style.display=att;}
	}

    function handleCloseImageViewer(){
    	// setIndex(0);
    	setImageViewerOpen(false);
    }

    const downloadFile = () => {
        handleClose();
		setDownloadFile(selectedFile, true);
		// setLoading(true);
        //setFirstDownload(true);
	}

	const downloadFile2 = () => {
		setDownloadFile(selectedFile);
		setLoading(true);
		//setFirstDownload(true);
		// setDownloadScreen(true);
	}
	
	const skipDownload = () =>{
		setDownloadScreen(true);
	}

	if (imageViewerOpen) {
		document.body.style.overflow = 'hidden';
	} else {
		document.body.style.overflow = 'auto';
	}

	return (
			<React.Fragment>
				{/* {isLoading && !props.showDownloadScreen ? <LoadingIndicator /> : null} */}
				
	            <Grid container>
					<Grid item xs={12}>
						<Grid container justify="flex-start" alignItems="flex-start">
							{ !isApprover && props.showDownloadScreen === false ?
								(
									<Grid item xs={8}>
										<Typography className={classes.title} color="textPrimary" variant="h5">
											Download Proposal
										</Typography>
										<Typography className={classes.subTitle} color="textPrimary">
											Download your approved proposal for client’s signature.
										</Typography>
									</Grid>
								) : (
									<Grid item xs={8}>
										<Typography className={classes.title} color="textPrimary" variant="h5">
											{
												!isApprover ? (
													<Typography className={classes.title} color="textPrimary" variant="h5">
														Finalize Proposal
													</Typography> )
												: (
													<Typography className={classes.title} color="textPrimary" variant="h5">
														Finalize Account
													</Typography> )
											}
										</Typography>
										<Typography className={classes.subTitle} color="textPrimary">
											Ensure that the attached file/s matches the proposal version
										</Typography>
									</Grid>
								)
							}
			                { (!isApprover && props.showDownloadScreen === true) || isApprover ?
								(<Grid item xs={4} className={classes.downloadReport}>
									{
										<span 
											ref={anchorRef}
											className={classes.iconsSpan}
											onClick={() => {
												if (!isApprover && uploaded.length > 0) {
													setDownloadFile("signed_proposal");
												} else {
													handleOpenPopper();
												}
											}}>

											<FontAwesomeIcon 
												icon={faDownload}
												className={classes.icons}
											/> {`Download${!isApprover ? (uploaded.length > 0 ? ' Signed ' : ' Approved ') : ' '}Proposal`}
											<Popper
												open={isPopoverOpen}
												anchorEl={anchorRef.current}
												placement={"bottom-end"}
												className={classes.popper}
												disablePortal={false}
												style={{marginTop:'4px'}}
												modifiers={{
													flip: {
													enabled: false,
													},
													preventOverflow: {
													enabled: true,
													boundariesElement: 'undefined',
													},
													arrow: {
													enabled: true,
													element: arrowRef,
													},
												}}
											>
												<span className={classes.arrow} ref={setArrowRef} />
												<Paper className={classes.paper}>
													<ClickAwayListener onClickAway={handleClose}>
														<Grid container>
															{
																(!isApprover && uploaded.length > 0) || (isApprover && downloaded.length > 0) ? 
																<React.Fragment>
																	<Grid item xs={12}>
																		<Typography className={classes.textLabel} color="textPrimary">
																			Choose File
																		</Typography>
																	</Grid>
																	<Grid item xs={12}>
																		<RadioGroup
																			aria-label="choose-data"
																			name="choose-data"
																			value={selectedFile}
																			style={{paddingLeft:'5px'}}
																			onChange={e => setSelectedFile(e.target.value)}>
																			{
																				hasSignedProposal ? (
																					<FormControlLabel
																						className={classes.text}
																						value="signed_proposal"
																						control={<Radio style={{padding:'5px'}} color="primary" />}
																						label={
																							<Typography className={classes.text}>
																								{isApprover ? 'Download Signed Proposal' : 'Signed Proposal only'}
																							</Typography>
																						}
																						labelPlacement="end"
																					/>
																				): null
																			}
																			
																			<FormControlLabel
																				className={classes.text}
																				value="this_version"
																				control={<Radio style={{padding:'5px'}} color="primary" />}
																				label={
																					<Typography className={classes.text}>
																						{isApprover ? 'Download Approved Proposal' : 'This version of the proposal'}
																					</Typography>
																				}
																				labelPlacement="end"
																			/>
																			{/* <FormControlLabel
																				className={classes.text}
																				value="both"
																				control={<Radio style={{padding:'5px'}} color="primary" />}
																				label={<Typography className={classes.text}>Both</Typography>}
																				labelPlacement="end"
																			/> */}
																		</RadioGroup>
																	</Grid>
																</React.Fragment> : null

															}
															
															{selectedFile === "this_version" ? (
																<>
																<Grid item xs={12}>
																	<Typography className={classes.textLabel} color="textPrimary">
																		Choose Format
																	</Typography>
																</Grid>
																<Grid item xs={12}>
																	<RadioGroup
																		aria-label="choose-format"
																		name="choose-format"
																		value={selectedFormat}
																		style={{paddingLeft:'5px'}}
																		onChange={e => setSelectedFormat(e.target.value)}>
																		<FormControlLabel
																			className={classes.text}
																			value="PDF"
																			control={<Radio style={{padding:'5px'}} color="primary" />}
																			label={<Typography className={classes.text}>PDF</Typography>}
																			labelPlacement="end"
																		/>
																		<FormControlLabel
																			className={classes.text}
																			value="DOCX"
																			disabled
																			control={<Radio style={{padding:'5px'}} color="primary" />}
																			label={<Typography className={classes.text}>DOC</Typography>}
																			labelPlacement="end"
																		/>
																	</RadioGroup>
																</Grid>
																</>
															) : null}
															
															<Grid item xs={12} style={{textAlign: 'center', marginTop: '10px'}}>
																<Button className={classes.downloadBtn} variant="contained" color="secondary" onClick={downloadFile}>
																	Download File
																</Button>
															</Grid>
														</Grid>
													</ClickAwayListener>
												</Paper>
											</Popper>
										</span>
									}
								</Grid>) : null
							}
						</Grid>
					</Grid>
					<React.Fragment>
						<div id='snkbrcont' style={{display:'none'}}> 
						<Snackbar
							id="rejmsg"
							key="close"
							anchorOrigin={{
								horizontal: 'center',
								vertical: 'top',
							}}
							open={true}
							autoHideDuration={3000}
							onClose={()=>{show('none');}}
						>
							<SnackbarContent
								style={{ backgroundColor: 'rgba(0, 0, 0, 0.75)' }}
								message={"Invalid File Dropped."}
							/>
						</Snackbar>
						</div>
					</React.Fragment>
					<Grid item xs={12}>
						<Grid container direction="row" justify="flex-start" alignItems="flex-start">
							<Grid item xs={12}>
								{ !isApprover && props.showDownloadScreen === false ?
									(
										<Grid container direction="row" justify="flex-start" alignItems="flex-start" className={classes.downloadContainer}>
											<Grid item xs={12}>
												<Typography className={classes.textLabel} color="textPrimary">
													Choose Format
												</Typography>
											</Grid>
											<Grid item xs={12}>
												<RadioGroup
													aria-label="choose-format"
													name="choose-format"
													value={selectedFormat}
													style={{paddingLeft:'5px'}}
													onChange={e => setSelectedFormat(e.target.value)}>
													<FormControlLabel
														className={classes.text}
														value="PDF"
														control={<Radio style={{padding:'5px'}} color="primary" />}
															label={<Typography className={classes.text}>PDF</Typography>}
															labelPlacement="end"
														/>
													<FormControlLabel
														className={classes.text}
														value="DOCX"
														disabled
														control={<Radio style={{padding:'5px'}} color="primary" />}
														label={<Typography className={classes.text}>DOC</Typography>}
														labelPlacement="end"
													/>
												</RadioGroup>
											</Grid>
											<Grid item xs={12} style={{marginTop: '10px'}}>
												<Button className={classes.downloadBtn2}  style={{marginRight: '20px'}}variant="contained" color="secondary" onClick={downloadFile2}>
													Download File
												</Button>
												<Button className={classes.downloadBtn} variant="contained" color="secondary" onClick={skipDownload}>
													Skip
												</Button>
											</Grid>
										</Grid>
									):(
										<Grid container direction="row" justify="flex-start" alignItems="flex-start" className={classes.uploadFilesContainer}>
											{
												!isApprover ? (
													uploaded.map((item:any, index:number) => {
														return (
															<Grid item className={classes.uploadFiles} >
																{
																	
																	(item.type.includes('doc')) ? (
																		<div className={classes.thumbnail}
																			onClick={(e)=> {
																				handleOpenImageViewer(index)
																				console.log('clicked ',e);
																			}}
																		>
																			<FontAwesomeIcon className="fa-4x" icon={faFileWord} style={{ color: 'rgba(39, 46, 76, 0.56)', position: 'absolute', top: '35%', bottom: '50%', left: '40%', right: '50%' }} />
																		</div>
																	) : (item.type.includes('pdf')) ? (
																		<div className={classes.thumbnail}
																			onClick={(e)=> {
																				handleOpenImageViewer(index)
																				console.log('clicked ',e);
																			}}
																		>
																			<FontAwesomeIcon className="fa-4x" icon={faFilePdf} style={{ color: 'rgba(39, 46, 76, 0.56)', position: 'absolute', top: '35%', bottom: '50%', left: '40%', right: '50%' }} />
																		</div>
																	) : (
																		<img src={item.url} className={classes.uploaded} 
																			onClick={(e)=> {
																				handleOpenImageViewer(index)
																				console.log('clicked ',e);
																			}}/>
																	)
																}
																<Typography color="textPrimary" style={{paddingTop:'5px', textAlign:'center', width: 182}}>
																	<span>
																		<span style={{textDecoration: 'none', fontSize:'10px'}}>{item.name}</span>
																		{
																			!isApprover ? (
																				<span 
																					style={{ paddingLeft: '10px', fontSize:'15px'}} 
																					onClick={e => {
																						setDeleteFile(index);
																						console.log(e);
																					}}
																				>
																					<FontAwesomeIcon icon={faTimesCircle} style={{ color: '#B5B8C1' }} />
																				</span>
																			) : null
																		}
																	</span>
																</Typography>
															</Grid>
														);
													})
												) : (
													downloaded.map((item:any, index:number) => {
														return (
															<Grid item className={classes.uploadFiles} >
																{
																	(item.type.includes('doc')) ? (
																		<div className={classes.thumbnail}
																			onClick={(e)=> {
																				handleOpenImageViewer(index)
																				console.log('clicked ',e);
																			}}
																		>
																			<FontAwesomeIcon className="fa-4x" icon={faFileWord} style={{ color: 'rgba(39, 46, 76, 0.56)', position: 'absolute', top: '35%', bottom: '50%', left: '40%', right: '50%' }} />
																		</div>
																	) : (item.type.includes('pdf')) ? (
																		<div className={classes.thumbnail}
																			onClick={(e)=> {
																				handleOpenImageViewer(index)
																				console.log('clicked ',e);
																			}}
																		>
																			<FontAwesomeIcon className="fa-4x" icon={faFilePdf} style={{ color: 'rgba(39, 46, 76, 0.56)', position: 'absolute', top: '35%', bottom: '50%', left: '40%', right: '50%' }} />
																		</div>
																	) : (
																		<img src={item.url} className={classes.uploaded} 
																			onClick={(e)=> {
																				handleOpenImageViewer(index)
																				console.log('clicked ',e);
																			}}/>
																	)
																}
																<Typography color="textPrimary" style={{paddingTop:'5px', textAlign:'center', width: 182}}>
																	<span>
																		<span style={{textDecoration: 'none', fontSize:'10px'}}>{item.name}</span>
																		{
																			!isApprover ? (
																				<span 
																					style={{ paddingLeft: '10px', fontSize:'15px'}} 
																					onClick={e => {
																						setDeleteFile(index);
																						console.log(e);
																					}}
																				>
																					<FontAwesomeIcon icon={faTimesCircle} style={{ color: '#B5B8C1' }} />
																				</span>
																			) : null
																		}
																	</span>
																</Typography>
															</Grid>
														);
													})
													// <Grid item className={classes.uploadFiles} >
													// 	<img src={sampleFile[0].src} className={classes.uploaded} 
													// 		onClick={(e)=> {
													// 			handleOpenImageViewer(0)
													// 			console.log('clicked ',e);
													// 		}}/>
													// 	<Typography color="textPrimary" style={{paddingTop:'5px', textAlign:'center'}}>
													//         <span>
													//             <span style={{textDecoration: 'none', fontSize:'10px'}}>{sampleFile[0].name}</span>
													//         </span>
													//     </Typography>
													// </Grid>
												)
											}
											{
													!isApprover ? (
														
													<Grid item xs={1} className={classes.dropzone} style={{
														display: 'flex',
														width: '182px',
														height: '160px',
														marginBottom: '46px',
														marginLeft: '55px'
													}}>
														<Dropzone
															onDropAccepted={acceptedFiles => setFileUpload(acceptedFiles)}
															onDropRejected={()=>{show('block');}}
															accept=".jpg, .jpeg, .pdf, .JPG, .PNG, .png, .docx">
															{({getRootProps, getInputProps}) => (
																<section>
																	<div className={classes.uploadFile} {...getRootProps()}>
																		<input id="upload_file_member" {...getInputProps()} />
																		<FontAwesomeIcon className="fa-2x" icon={faFileUpload} style={{ color: 'rgba(39, 46, 76, 0.56)' }}/>
																		<p style={{ color: 'rgba(39, 46, 76, 0.56)', fontWeight: 400 }}>
																			Drag or drop file
																		</p>
																	</div>
																</section>
															)}
														</Dropzone>
													
													</Grid>
												) : null
											}
										</Grid>
									)
								}
							</Grid>
						</Grid>		
					</Grid>
				</Grid>
				{ imageViewerOpen ?
                    (
                    	!isApprover ? (
							props.showDownloadScreen === true ?
								(
									<ImageViewer
										images={uploaded}
										index={index}
										onClose={handleCloseImageViewer}
									/>
								) : null
                    	) : (
                    		<ImageViewer
								images={downloaded}
								index={index}
								onClose={handleCloseImageViewer}
							/>
						)
                    ) : null
                }


			</React.Fragment>
	);
};