/**
 * The global state selectors
 */

import { createSelector } from 'reselect';

const combineNodes = (parentNodes, childNodes) => {
  const nodes = parentNodes.reduce ((result, parent) => {
    parent.children.map ((child) => {
      let foundChild = childNodes.find ((childNode) => child === childNode.id);

      if (foundChild) {
        let child = Object.assign({}, foundChild);

        child['parent_name'] = parent.name;
        child['parent_code'] = parent.code;
        child['disabled'] = false;
        result.push(child);
      }
    });

    return result;
  }, []);

  return nodes;
};

const combineNodes2 = (parentNodes, childNodes) => {
  const nodes = parentNodes.reduce ((result, parent) => {

    let newParent = Object.assign({}, parent);
    let newChildNodes: any[] = []; 

    newParent.children.map ((child) => {
      let foundChild = childNodes.find ((childNode) => child === childNode.id);

      if (foundChild) {
        let child = Object.assign({}, foundChild);

        child['disabled'] = false;
        newChildNodes.push(child);
      }
    });

    newParent['child_nodes'] = newChildNodes;
    newParent['disabled'] = false;

    result.push(newParent);

    return result;
  }, []);

  return nodes;
};


const getFilteredNodes = (nodes, level) => {
  return nodes.filter((item: any) => {
    return (
      item.level === level
    );
  });
};

const getSidebarList = (nodes, planType, planPackage) => {
  return nodes.filter((item: any) => {
    return (
      item.tree_path[2] === planType &&
      item.tree_path[3] === planPackage
    );
  });
};


const getRows = (exclusionList, benefitCategoryNodes, benefitTypeNodes, mainBenefitNodes, subBenefitNodes, selectedPlanType, selectedPlanPackage, selectedBenefitCategory) => {
  let rows: any[] = [];
  if (['EX'].includes(selectedBenefitCategory)) {
    if (exclusionList.length > 0) {
      rows = exclusionList.reduce ((result, exclusionArray) => {
        const code = exclusionArray[0].code;
        
        if (code === selectedPlanType) {
          let exclusion = Object.assign({}, exclusionArray[exclusionArray.length - 1]);

          const parent = Object.assign({}, exclusionArray[exclusionArray.length - 2]);

          exclusion['parent_name'] = parent.name;
          exclusion['parent_code'] = parent.code;
          exclusion['disabled'] = false;
          exclusion['included'] = false;

          const found = benefitTypeNodes.find((item: any) => {
            return item.id === exclusion.id;
          });

          if (found && found['custom_metadata'] && found['custom_metadata']['is_excluded'] !== undefined) {
            const metadata = Object.assign({}, found['custom_metadata']);
            exclusion['included'] = metadata['is_excluded'];
          }

          result.push(exclusion);
        }

        return result;
      }, []);
    }
  } else if (['ID','NT'].includes(selectedBenefitCategory)) {
    const categoryNodes = benefitCategoryNodes.filter ((item: any) => {
      return item.tree_path[2] === selectedPlanType &&
      item.tree_path[3] === selectedBenefitCategory;
    });
    const typeNodes = benefitTypeNodes.filter((item: any) => {
      return (
        item.tree_path[2] === selectedPlanType  &&
        item.tree_path[3] === selectedBenefitCategory
      );
    });

    rows = combineNodes2 (categoryNodes, typeNodes);

  } else if (['EL'].includes(selectedBenefitCategory)) {
    const benefitNodes = mainBenefitNodes.filter((item: any) => {
      return (
        item.tree_path[2] === selectedPlanType  &&
        item.tree_path[3] === selectedPlanPackage &&
        item.tree_path[4] === selectedBenefitCategory &&
        item.tree_path[5] === 'DP'
      );
    });

    const subNodes = subBenefitNodes.filter((item: any) => {
      return (
        item.tree_path[2] === selectedPlanType  &&
        item.tree_path[3] === selectedPlanPackage &&
        item.tree_path[4] === selectedBenefitCategory &&
        item.tree_path[5] === 'DP'
      );
    });

    rows = combineNodes2 (benefitNodes, subNodes);

    if (selectedPlanType !== 'BASE') {
        rows = rows.map ((item) => {
          item.child_nodes.map((child) => {
            const baseNode = subBenefitNodes.find ((baseItem) => {
              return (
                baseItem.name === child.name &&
                baseItem.code === child.code &&
                baseItem.tree_path[2] === 'BASE' &&
                baseItem.tree_path[3] === child.tree_path[3] &&
                baseItem.tree_path[4] === child.tree_path[4] &&
                baseItem.tree_path[5] === child.tree_path[5] &&
                baseItem.tree_path[6] === child.tree_path[6] &&
                baseItem.tree_path[7] === child.tree_path[7]
              )
            });

            if (baseNode && baseNode.included === true) {
              child['disabled'] = true;
            } else {
              child['disabled'] = false;
            }

            return child;
          })
          return item;
      });
    }

  } else if (['RBC'].includes(selectedBenefitCategory))  {
    rows = [...benefitCategoryNodes];

    rows = rows.filter((item: any) => {
      return (
        item.tree_path[2] === selectedPlanType  &&
        item.tree_path[3] === selectedPlanPackage 
      );
    });

    rows = rows.map ((item) => {
      item['disabled'] = false;
      return item;
    });
  } else if (['EM','DNT','CC','EC','PV','SH','ADD','SD'].includes(selectedBenefitCategory)) {
    rows = [...benefitTypeNodes];

    rows = rows.filter((item: any) => {
      return (
        item.tree_path[2] === selectedPlanType  &&
        item.tree_path[3] === selectedPlanPackage &&
        item.tree_path[4] === selectedBenefitCategory
      );
    });

    if (selectedPlanType !== 'BASE' && selectedBenefitCategory !== 'SH') {
      rows = rows.map ((item) => {
        const baseNode = benefitTypeNodes.find ((baseItem) => {
          return (
            baseItem.name === item.name &&
            baseItem.code === item.code &&
            baseItem.tree_path[2] === 'BASE' &&
            baseItem.tree_path[3] === item.tree_path[3] &&
            baseItem.tree_path[4] === item.tree_path[4] &&
            baseItem.tree_path[5] === item.tree_path[5] 
          )
        });

        if (baseNode && baseNode.included === true) {
          item['disabled'] = true;
        } else {
          item['disabled'] = false;
        }

        return item;
      });
    } else {
      rows = rows.map ((item) => {
        item['disabled'] = false;
        return item;
      });
    }
    return rows;
  } else if (['MT','APE', 'IP', 'OP', 'OM','PPE'].includes(selectedBenefitCategory)) {
    const typeNodes = benefitTypeNodes.filter ((item: any) => {
      return item.tree_path[2] === selectedPlanType &&
      item.tree_path[4] === selectedBenefitCategory;
    });
    const benefitNodes = mainBenefitNodes.filter((item: any) => {
      return (
        item.tree_path[2] === selectedPlanType &&
        item.tree_path[3] === selectedPlanPackage &&
        item.tree_path[4] === selectedBenefitCategory
      );
    });

    rows = combineNodes (typeNodes, benefitNodes);

    if (selectedPlanType !== 'BASE') {
      rows = rows.map ((item) => {
        const baseNode = mainBenefitNodes.find ((baseItem) => {
          return (
            baseItem.name === item.name &&
            baseItem.code === item.code &&
            baseItem.tree_path[2] === 'BASE' &&
            baseItem.tree_path[3] === item.tree_path[3] &&
            baseItem.tree_path[4] === item.tree_path[4] &&
            baseItem.tree_path[5] === item.tree_path[5] &&
            baseItem.tree_path[6] === item.tree_path[6]
          )
        });

        if (baseNode && baseNode.included === true) {
          item['disabled'] = true;
        } else {
          item['disabled'] = false;
        }

        return item;
      });
    }

  } else {
    rows = mainBenefitNodes.filter((item: any) => {
        return (
          item.tree_path[2] === selectedPlanType &&
          item.tree_path[3] === selectedPlanPackage &&
          item.tree_path[4] === selectedBenefitCategory
        );
      }
    );
  }

  if (selectedBenefitCategory !== 'EX') {
    rows = rows.filter((item: any) => {
      if (item['custom_metadata'] && item['custom_metadata']['standard_exclusion'] !== undefined) {
        return item['custom_metadata']['standard_exclusion'] ? false : true;
      }
      return true;
    });

  } 

  return rows;
};

const getPlanTypeList = (planTypeNodes, mainBenefitNodes, benefitTypeNodes, subBenefitNodes) => {
  console.debug(benefitTypeNodes);
  const coverageCategories = ['EM','SD','PV'];
  const riderCategories = ['DNT','EC','CC','SH','ADD'];
  // const combinedCategories = [...coverageCategories, ...riderCategories];
  const combinedNodes = [...mainBenefitNodes, ...benefitTypeNodes, ...subBenefitNodes];

  return planTypeNodes.map((item: any) => {
    item['count'] = combinedNodes.reduce ((result, benefit) => {
        if (  benefit.level === 6 &&
            benefit.tree_path[2] === item.code &&
            benefit.included && 
            ['CV'].includes(benefit.tree_path[3]) &&  
            coverageCategories.includes(benefit.tree_path[4])
        ) {
        if (  benefit.tree_path[2] !== 'BASE' ) {
          const baseNode = benefitTypeNodes.find ((baseItem) => {
            return (
              baseItem.name === benefit.name &&
              baseItem.code === benefit.code &&
              baseItem.tree_path[2] === 'BASE' &&
              baseItem.tree_path[3] === benefit.tree_path[3] &&
              baseItem.tree_path[4] === benefit.tree_path[4] &&
              baseItem.tree_path[5] === benefit.tree_path[5] 
            )
          });

          if (baseNode && !baseNode.included) {
            console.debug("RESULT 3", benefit.name);
            result++;
          } 
        } else {
          console.debug("RESULT 4", benefit.name, benefit.included);
          result++;
        }
      } else if (  benefit.level === 6 &&
            benefit.tree_path[2] === item.code &&
            benefit.included && 
            ['RI'].includes(benefit.tree_path[3]) &&  
            riderCategories.includes(benefit.tree_path[4]) 
        ) {
        if (  benefit.tree_path[2] !== 'BASE' ) {
          const baseNode = benefitTypeNodes.find ((baseItem) => {
            return (
              baseItem.name === benefit.name &&
              baseItem.code === benefit.code &&
              baseItem.tree_path[2] === 'BASE' &&
              baseItem.tree_path[3] === benefit.tree_path[3] &&
              baseItem.tree_path[4] === benefit.tree_path[4] &&
              baseItem.tree_path[5] === benefit.tree_path[5] 
            )
          });

          if (baseNode && !baseNode.included && benefit.tree_path[4] !== 'SH') {
            console.debug("RESULT 6", benefit.name);
            result++;
          } 
        } else {
          console.debug("RESULT 7", benefit.name, benefit.included);
          result++;
        }
      } else if ( benefit.level === 7 &&
                benefit.tree_path[2] === item.code && 
                benefit.included &&
                !['EM', 'EC', 'EL'].includes(benefit.tree_path[4])
          ) {
        if (benefit.tree_path[2] !== 'BASE') {
          const baseNode = mainBenefitNodes.find ((baseItem) => {
            return (
              baseItem.name === benefit.name &&
              baseItem.code === benefit.code &&
              baseItem.tree_path[2] === 'BASE' &&
              baseItem.tree_path[3] === benefit.tree_path[3] &&
              baseItem.tree_path[4] === benefit.tree_path[4] &&
              baseItem.tree_path[5] === benefit.tree_path[5] &&
              baseItem.tree_path[6] === benefit.tree_path[6]
            )
          });

          if (baseNode && !baseNode.included) {
            console.debug("RESULT 1", benefit.name);
            result++;
          } 
        } else {
          console.debug("RESULT 2", benefit.name, benefit.included);
          result++;
        }
      } else if ( benefit.level === 8 &&
                benefit.tree_path[2] === item.code && 
                benefit.included  &&
                !['OP'].includes(benefit.tree_path[4])
          ) {
        if (benefit.tree_path[2] !== 'BASE') {
          const baseNode = mainBenefitNodes.find ((baseItem) => {
            return (
              baseItem.name === benefit.name &&
              baseItem.code === benefit.code &&
              baseItem.tree_path[2] === 'BASE' &&
              baseItem.tree_path[3] === benefit.tree_path[3] &&
              baseItem.tree_path[4] === benefit.tree_path[4] &&
              baseItem.tree_path[5] === benefit.tree_path[5] &&
              baseItem.tree_path[6] === benefit.tree_path[6]
            )
          });

          if (baseNode && !baseNode.included) {
            console.debug("RESULT 1", benefit.name);
            result++;
          } 
        } else {
          console.debug("RESULT 2", benefit.name, benefit.included);
          result++;
        }
      }
      return result;
    }, 0);

    return item;
  });
};

const getContractNode = (contractNodes) => {
  return contractNodes.find(item => 
    item.code === 'CT'
  );
};

const getCategoryNode = (planPackageNodes, benefitCategoryNodes, selectedPlanType, selectedPlanPackage, selectedBenefitCategory) => {
  if (['RBC', 'EX'].includes(selectedBenefitCategory)) {
    return planPackageNodes.find(item => 
      item.code === selectedPlanPackage &&
      item.tree_path[2] === selectedPlanType && 
      item.tree_path[3] === selectedPlanPackage 
    );
  } else if (selectedBenefitCategory === 'NT') {
      return planPackageNodes.find(item => 
        item.code === selectedBenefitCategory &&
          item.tree_path[2] === selectedPlanType && 
          item.tree_path[3] === selectedBenefitCategory 
        );
  } else {
    return benefitCategoryNodes.find(item => 
      item.code === selectedBenefitCategory &&
      item.tree_path[2] === selectedPlanType && 
      item.tree_path[3] === selectedPlanPackage && 
      item.tree_path[4] === selectedBenefitCategory
    );
  }
};

