const selectRows = createSelector(getExclusionList, selectBenefitCategoryNodes, selectBenefitTypeNodes, selectMainBenefitNodes, selectSubBenefitNodes, getSelectedPlanType, getSelectedPlanPackage, getSelectedBenefitCategory, (exclusionList, benefitCategoryNodes, benefitTypeNodes, mainBenefitNodes, subBenefitNodes, selectedPlanType, selectedPlanPackage, selectedBenefitCategory) => {
  return getRows(exclusionList, benefitCategoryNodes, benefitTypeNodes, mainBenefitNodes, subBenefitNodes, selectedPlanType, selectedPlanPackage, selectedBenefitCategory);
});

