const formatNode = (nodes: any[], item: any, clientParam: any) => {
	let newItem = Object.assign({}, item);

	newItem.children = newItem.children.filter(child =>
      nodes.find((node) => node.id === child) ? true : false
    );

    if (newItem.code === 'CT') {
    	newItem['principals_count'] = parseInt(clientParam.principalData.numOfPrincipals);
    	newItem['male_principals_count'] = parseInt(clientParam.principalData.malePrincipals);
    	newItem['female_principals_count'] = parseInt(clientParam.principalData.femalePrincipals);
    	newItem['dependents_count'] = parseInt(clientParam.principalData.numOfDependents);
    	newItem['male_dependents_count'] = parseInt(clientParam.principalData.maleDependents);
    	newItem['female_dependents_count'] = parseInt(clientParam.principalData.femaleDependents);
    }

    if (newItem.code == 'OD' || newItem.code == 'CB') {
		if (newItem['custom_metadata']['partner_network_list'] && newItem['custom_metadata']['partner_network_list'].length > 0) {

			const newPartnerNetworkList = [...item['custom_metadata']['partner_network_list']];

			let includedFacilities: any[] = [];
			let excludedFacilities: any[] = [];

			newItem['custom_metadata']['partner_network_list'] = newPartnerNetworkList.map((partner: any) => {
				let newPartner: any = Object.assign({}, partner);

				if (newPartner['included'] !== undefined) {
					if (newPartner['included'] === true) {
						delete newPartner.included;
						includedFacilities.push(newPartner);
					} else if (newPartner['included'] === false) {
						delete newPartner.included;
						excludedFacilities.push(newPartner);
					}
				}
			});

			delete newItem.custom_metadata.partner_network_list;

			newItem['custom_metadata']['included_facilities'] = includedFacilities;
			newItem['custom_metadata']['excluded_facilties'] = excludedFacilities;

		}
	}

	delete newItem.tree_path;
	delete newItem.tree_id;
	delete newItem.included;
	delete newItem.count;
	delete newItem.level;
	delete newItem.edited;
	delete newItem.node_path;

	if (newItem['original_id']) {
		newItem._id = newItem['original_id']
		delete newItem.id; 
	}

	if (newItem['original_children']) {
		newItem.children = newItem['original_children']
	}

	delete newItem.original_node;
	delete newItem.original_children;
	delete newItem.original_id;
	return newItem;
}

// const getEditedNodesPayload = (nodeParam: any[], deletedNodeListParam: any[], newNodeListParam: any[], editedNodeListParam: any[]) => {
export const getEditedNodesPayload = (initialNodeParam: any[], nodeParam: any[], editedNodeListParam: any[], clientParam: any, status?: string) => {
	let nodes = [...nodeParam];
	let newNodeRef = [...nodeParam];

	for (let x = 8; x > 3; x--) {
		newNodeRef = newNodeRef.filter ((item: any) => GlobalFunction.filterNodesByLevel(item, nodes, x));

		newNodeRef = newNodeRef.map ((item: any) => {
			let newItem = Object.assign({}, item);

			newItem.children = newItem.children.filter(child =>
	          newNodeRef.find((node) => node.id === child) ? true : false
	        );
	        return newItem;
		});
	}

	newNodeRef = newNodeRef.map ((item: any) => {
		let newItem = Object.assign({}, item);

		newItem.children = newItem.children.filter(child =>
          nodes.find((node) => node.id === child) ? true : false
        ); 

		return newItem;
    });

	let newNodes: any[] = newNodeRef.reduce ((result, item) => {
		if (!item['original_id']) {
			const tree = GlobalFunction.getNodeTree(item, nodes);
			if (tree.length > 1) {
				const id = tree[tree.length-1].id;
				const parentID = tree[tree.length-2].id;

				const parent = nodes.find ((item) => {
					return parentID === item.id;
				});

				if (parent) {
					let newNode: object = formatNode(nodes, item, clientParam);

					if (parent['original_id'] !== undefined) {
						newNode['parent_id'] = parent['original_id'];
					}

					if (parent['included'] === true && parent['children'].includes(id)) {
						result.push(newNode);
					}
				}
			}
		} 
		return result;
	}, new Array());

	let deletedNodes: any[] = initialNodeParam.reduce ((result, item) => {
		const id = item.id;
		if (item['original_id'] !== undefined) {
			const found = newNodeRef.find((item) => {
				return id === item.id
			});

			if (!found) {
				const originalID: string = item['original_id'];
				result.push(originalID);
			}
		} 

		return result;
	}, new Array());
	
	let editedNodes: any[] = editedNodeListParam.reduce((result, tree) => {
		const id = tree[tree.length-1].id;
		const found = nodes.find ((item) => {
			return id === item.id;
		});

		if (found) {
			let editedNode: any = formatNode(nodes, found, clientParam);

			delete editedNode.children;
			
			result.push(editedNode);
		}

		return result;
	}, new Array());

	const contractNode = nodes.find(item => 
	    item.code === 'CT'
	);

	if (contractNode) {
		let editedNode: any = formatNode(nodes, contractNode, clientParam);
		delete editedNode.children;
		editedNodes.push(editedNode);
	}

	const motherContractNode = nodes.find(item=>
		item.code == 'MCT'
	);
	if(motherContractNode && status !== undefined){
		let editedNode: any = formatNode(nodes, motherContractNode, clientParam);
		editedNode.status = status;
		editedNodes.push(editedNode);
	}
	return {deletedNodes, newNodes, editedNodes};
}

export const getEditedRevisionNodesPayload = (initialNodeParam: any[], clientParam: any, status?: string) => {
	let nodes = [...initialNodeParam];
	let editedNodes: any[] = [];

	const motherContractNode = nodes.find(item=>
		item.code == 'MCT'
	);
	if(motherContractNode && status !== undefined){
		let editedNode: any = formatNode(nodes, motherContractNode, clientParam);
		editedNode.status = status;
		editedNodes.push(editedNode);
	}
	return editedNodes;
}

