import React, { FC, useState, useEffect } from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Grid from '@material-ui/core/Grid';
import { makeStyles, Theme } from '@material-ui/core/styles';
import clsx from 'clsx';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faMinusCircle } from '@fortawesome/free-solid-svg-icons';

import { Components } from '@hims/core';

import { IEditPlanTypeModal } from './IEditPlanTypeModal';

const InputTextField = Components.UI.InputTextField.InputTextField;

const useStyles = makeStyles((theme: Theme) => ({
	dialog: {},
	dialogAction: {
		marginBottom: theme.spacing(2),
		paddingTop: theme.spacing(4),
		justifyContent: 'center'
	},
	container: {
        paddingRight:'15px',
        paddingLeft:'15px',
        maxWidth: '500px'
	},
	buttons: {
		minWidth: 115,
		marginRight: '20px'
	}
}));

const numallowedpattern = new RegExp(/\D/);

export type IDataField = IEditPlanTypeModal.IDataField;

export const EditPlanTypeModal: FC<IEditPlanTypeModal.IProps> = props => {
	const classes = useStyles();
	const [name, setName] = useState<string>('');
	const [nameError, setNameError] = useState<boolean>(false);
	const [nameErrorMessage, setNameErrorMessage] = useState('');
	const [iPrincipal, setIPrincipal] = useState<string>('0');
	const [iPrincipalError, setIPrincipalError] = useState<boolean>(false);
	const [iDependent, setIDependent] = useState<string>('0');
	const [iDependentError, setIDependentError] = useState<boolean>(false);
	const [requestedRate, setRequestedRate] = useState<number>(0);
	const [formattedRequestedRate, setFormattedRequestedRate] = useState<string>('0');
	const [requestedRateError, setRequestedRateError] = useState<boolean>(false);

	const trimByChar = (s: string, c: string) => {
		if (c === "]") c = "\\]";
		if (c === "\\") c = "\\\\";
		return s.replace(new RegExp(
		  "^[" + c + "]+$", "g"
		), "");
	};

	const getFormattedNumber = (val: number): string => {
		
		let frr = val.toString();

		if(val > 999){
			let wholeVal = Math.floor(val);
			let decimal = val - wholeVal;
			let wholeValStr = wholeVal.toString();
			
			let extraIndex = wholeValStr.length % 3;
			if(extraIndex === 0){
				let otherArr = wholeValStr.match(/.{1,3}/g);
				if(otherArr){
					frr = otherArr.join(',') + (decimal > 0 ? ('.' + decimal) : '');
				}
			}
			else{
				let start = wholeValStr.substring(0, extraIndex);
				let other = wholeValStr.substring(extraIndex);
				let otherArr = other.match(/.{1,3}/g);
				if(otherArr){
					frr = start + ',' + otherArr.join(',') + (decimal > 0 ? ('.' + decimal) : '');
				}
			}
		}

		return frr;
	};

	const onRenewalRateChange = (value: string) => {
		value = value.replace(/ /g, '');
		value = value.replace(/,/g, '');
		value = trimByChar(value, '0');
		if(value.length===0){
			value = '0';
		}
		
		let val: number = parseFloat(value);
		if(isNaN(val)){
			val = 0;
		}
		
		let frr = getFormattedNumber(val);
		
		setRequestedRate(val);
		setFormattedRequestedRate(frr);
	};

	useEffect(() => {
		if(props.benefitNode){
			if(props.benefitNode.name && props.benefitNode.name !== undefined){
				setName(props.benefitNode.name);
			}
			if(props.benefitNode.custom_metadata && props.benefitNode.custom_metadata){
				if(props.benefitNode.custom_metadata.requested_rate 
					&& props.benefitNode.custom_metadata.requested_rate !== undefined){
						let req_rate: number = props.benefitNode.custom_metadata.requested_rate;
						let freq_rate: string = getFormattedNumber(req_rate);
						setRequestedRate(req_rate);
						setFormattedRequestedRate(freq_rate);
				}
				if(props.benefitNode.custom_metadata.indicative_principals 
					&& props.benefitNode.custom_metadata.indicative_principals !== undefined){
						setIPrincipal(props.benefitNode.custom_metadata.indicative_principals);
				}
				if(props.benefitNode.custom_metadata.indicative_dependents 
					&& props.benefitNode.custom_metadata.indicative_dependents !== undefined){
						setIDependent(props.benefitNode.custom_metadata.indicative_dependents);
				}
			}
		}
	}, [props.benefitNode]);

	const reset = () => {
		setName('');
		setNameError(false);
		setIPrincipal('0');
		setIPrincipalError(false);
		setIDependent('0');
		setIDependentError(false);
		setRequestedRate(0);
		setFormattedRequestedRate('0');
		setRequestedRateError(false);
		// setCode('');
		// setCodeError(false);
	};

	const handleClose = () => {
		reset();
		props.onClose();
	};

	const handleSubmit = () => {
		let error = false;
		if (name.length === 0) {
			setNameError(true);
			error = true;
		}
		const index = props.planTypeList.findIndex(item => 
		    item.id !== props.plan_type_id && item.name.toLowerCase() === name.toLowerCase()
		);
		if(index !== undefined && index !== -1){
			setNameError(true);
			setNameErrorMessage("Name already exists.");
			error = true;
		}

		if (parseFloat(iPrincipal) < 0) {
			setIPrincipalError(true);
			error = true;
		}

		if (parseFloat(iDependent) < 0) {
			setIDependentError(true);
			error = true;
		}

		if (!numallowedpattern.test(formattedRequestedRate) || requestedRate <= 0 ) {
			setRequestedRateError(true);
			error = true;
		}

		if (!error) {
			const data: IDataField = {
				name: name,
				title: "Benefits for " + name,
				indicative_principals: parseFloat(iPrincipal),
				indicative_dependents: parseFloat(iDependent),
				requested_rate: requestedRate,
			};

			reset();
			props.onSubmit(data, props.plan_type_id, props.plan_type_name);
			props.onClose();
		}
	};

	return (
		<Dialog
			open={props.isModalOpen}
			onClose={handleClose}
			aria-labelledby="form-dialog-title"
			maxWidth="sm"
			className={classes.dialog}
		>
			<div>
                <Grid container className={classes.container} justify="flex-start" alignItems="flex-start" direction="column">
                    <Grid item xs>
						<DialogTitle id="form-dialog-title">Edit Member Plan Type</DialogTitle>
					</Grid>
					<Grid item xs>
						<DialogContent>
							<Grid container spacing={2} justify="space-between" alignItems="flex-start">
								<Grid item xs={12}>
									<InputTextField
										label="Plan Type Name*"
										value={name}
										error={nameError}
										errorMessage={nameErrorMessage}
										onChange={(value: string) => {
											setName(value);
											setNameError(value.length === 0);
											setNameErrorMessage('');
										}}
									/>
								</Grid>
								{/*<Grid item xs={12}>
									<InputTextField
										label="Plan Type Code"
										value={code}
										error={codeError}
										onChange={(value: string) => {
											setCode(value);
											setCodeError(value.length === 0 || value === 'BASE');
										}}
									/>
								</Grid>*/}
								<Grid item xs={6}>
									<InputTextField
										type="number"
										label="Indicative No. of Principals"
										value={iPrincipal}
										error={iPrincipalError}
										onChange={(value: string) => {
											setIPrincipal(value);
											setIPrincipalError(parseInt(value) < 0);
										}}
									/>
								</Grid>
								<Grid item xs={6}>
									<InputTextField
										type="number"
										label="Indicative No. of Dependents"
										value={iDependent}
										error={iDependentError}
										onChange={(value: string) => {
											setIDependent(value);
											setIDependentError(parseInt(value) < 0);
										}}
									/>
								</Grid>
								<Grid item xs={6}>
									<InputTextField
										type="tel"
										label="Requested Rate*"
										value={formattedRequestedRate}
										error={requestedRateError}
										onChange={onRenewalRateChange}
									/>
								</Grid>
								<Grid item xs={12} style={{paddingTop: '15px'}}>
									<span onClick={props.onDelete}>
										<FontAwesomeIcon
											className="fa-sm"
											icon={faMinusCircle}
											style={{ color: '#9EA3AC', fontSize: '15px', marginRight:'5px'}}
										/> Delete this Plan Type
									</span>
								</Grid>
							</Grid>
						</DialogContent>
						<DialogActions className={clsx(classes.dialogAction)}>
								<Button onClick={handleClose} variant="outlined" color="default" className={classes.buttons}>
									Cancel
								</Button>
								<Button
									onClick={handleSubmit}
									variant="contained"
									color="primary"
									className={classes.buttons}
								>
									Save
								</Button>
						</DialogActions>
					</Grid>
				</Grid>
			</div>
		</Dialog>
	);
};

EditPlanTypeModal.defaultProps = {
	isModalOpen: false,
};
