function MyFloatingButtons(props){
    const buttonclasses = ButtonStyle();
    return(
        <div>
            <FloatingButtons
                leftButtons={
                    (<div>
                        
	                    {/*<Button
	                        className={buttonclasses.leftButton}
	                        style={{marginRight: '10px'}}
	                        variant="contained"
	                        color="secondary"
	                        size="small">
	                        <div style={{ display: 'flex', flexDirection: 'column' }}>
	                            <Typography className={buttonclasses.buttonTitle}>
	                                Save For Now
	                            </Typography>
	                            <Typography className={buttonclasses.buttonSubTitle}>
	                                Auto-Saved 5 minutes ago
	                            </Typography>
	                        </div>
	                    </Button>*/}
                        {(props.currentStep !== 2 && props.isApprover) || (props.currentStep !== 1 && !props.isApprover) ? 
                            <Button 
                                className={buttonclasses.root}
                                variant="contained" 
                                onClick={props.setStepForward} 
                                id="proposal_next" 
                                color="primary" >
                                Next
                            </Button> :

                            <React.Fragment>
                                {
                                    props.isApprover ? (
                                        <Button
                                            className={buttonclasses.root}
                                            variant="contained"
                                            color="primary"
                                            size="small"
                                            onClick={props.submitForSetup}
                                        >
                                            Approve
                                        </Button>
                                    ) : (
                                        <Button
                                            className={buttonclasses.root}
                                            variant="contained"
                                            color="primary"
                                            size="small"
                                            onClick={props.submitForSetup}
                                        >
                                            Submit for Setup
                                        </Button>
                                    )
                                }
                            </React.Fragment>

                        }
    	                    
                    </div>
                    )
                }
                rightButtons={
                	(<div>
                		{props.currentStep !== 0 ?
                        <Button 
                            className={buttonclasses.rightButton}
                            variant="contained"
                            onClick={props.setStepBack}
                            id="proposal_previous"
                            color="primary"
                        >
                                Back
                            </Button> : null
                        }
                        {/* <Button
                            className={buttonclasses.rightButton}
                            variant="contained"
                            size="small"
                            onClick={props.cancelPage}
                        >
                            Cancel
                        </Button> */}
                    </div>
                    )
                }
            />
        </div>
    )
}

