		// 	if (
		// 		item.level <= 6 &&
		// 		item.tree_path[1] === nodePath[1] &&
		// 		item.tree_path[2] === nodePath[2] &&
		// 		item.tree_path[3] === nodePath[3] &&
		// 		item.tree_path[4] === nodePath[4]
		// 	) {
		// 		let newItem = Object.assign({}, item);
		// 		newItem['included'] = false;

		// 		return newItem;
