import React from 'react';

import { useEffect } from 'react';
import Typography from '@material-ui/core/Typography';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import { GlobalFunction } from 'Components/UI/GlobalFunction';

import { IApproveProposal } from 'Pages/ApproveProposal/IApproveProposal';

import {
	Grid,
} from '@material-ui/core';

import { 
	LimitTypeItems,
	EligibleAvaileesItems,
	MaternityEligibleAvaileesItems
} from 'Components/UI/TableComponent/DropdownConstants';

interface SummaryProps {
	classes: any;
}

const AvaileeItems = [...EligibleAvaileesItems, MaternityEligibleAvaileesItems];

export const Summary:React.FC<IApproveProposal.IProps & SummaryProps> = (props: IApproveProposal.IProps & SummaryProps) => {
	const { classes, 
			summaryPackages, 
			summaryBenefits, 
			selectedSummaryCategory,
			SetSelectedSummaryPackage, 
			SetSelectedSummaryCategory 
		} = props;


	useEffect(() => {
		if (summaryPackages.length > 0) {
			const first = summaryPackages[0];
			SetSelectedSummaryPackage(`${first.tree_path[3]}`);
			SetSelectedSummaryCategory(`${first.code}`);
		}
	}, []);

	return (
			<Grid container className={classes.gridContainer}>
				<Grid item xs={12}>
					<Typography className={classes.sectionTitle}>
						Summary Per Plan Type
					</Typography>
			        <Tabs value={selectedSummaryCategory} variant='scrollable' className={classes.tabs}>
			         {
			         	summaryPackages.length > 0 ? 
			         		summaryPackages.map((item: any, i: number) => 
			         			 <Tab 
			         			 	key={i}
			         			 	value={item.code}
			         			 	label={item.name}
			         			 	className={classes.tab}
									onClick={() => {
										SetSelectedSummaryPackage(`${item.tree_path[3]}`);
										SetSelectedSummaryCategory(`${item.code}`);
									}}
			         			 />
							) : null
			         }
			        </Tabs>

				    <Table className={classes.table}>
				        <TableHead>
				          	<TableRow>
					            <TableCell className={classes.columnCategory}></TableCell>
					            <TableCell className={classes.columnBenefit}>Benefit</TableCell>
					           	<TableCell className={classes.columnLimit}>Limit</TableCell>
					           	<TableCell className={classes.columnEligibility}>Eligibility</TableCell>
					    	{/*     
					    		<TableCell>Effectivity</TableCell>
					            <TableCell>Reimbursement</TableCell>
					    	*/}
				          	</TableRow>
				        </TableHead>
				        <TableBody>
				        	{
				        		summaryBenefits.length > 0 ? 
				        			summaryBenefits.map((planType, i) => 
				        				planType['child_nodes'] && planType['child_nodes'].length > 0 ? 
				        					planType['child_nodes'].map ((row, x) => 
									            <TableRow className={i % 2 ? classes.tableRowEven : classes.tableRowOdd} key={x}>
									              { 
									              	x === 0 ? 
									              		<TableCell component="th" scope="row" rowSpan={planType['child_nodes'].length}>
											            	{planType.name}
											            </TableCell> : null

									              }    
									              <TableCell>
									              	{row.name}
									              </TableCell>
									              <TableCell>
									              {
														row.limits && row.limits.length > 0 ?
															row.limits.map((lim: any, i: number) => {
																const found = LimitTypeItems.find ((item) => item.type === lim.type && item.limit_unit === lim.limit_unit);
																let name = '';
																let suffix = '';
																let value = lim.limit;

																if (found) {
																	name = found.label + ": ";
																}

																if (lim.limit_unit === 'PHP') {
																	suffix = 'PHP';
																	value = GlobalFunction.formatNumber(value);
																}

																if (lim.limit_unit === 'Percentage') {
																	suffix = '%';

																	if (['ABL', 'MBL'].includes(lim.type)) {
																		suffix = '% ' + lim.type;
																	}
																}

																if (lim.limit_unit === 'Sessions') {
																	suffix = lim.limit == 1 ? 'session' : 'sessions';
																}

																if (lim.limit_unit === 'Visits') {
																	suffix = lim.limit == 1 ? 'visit' : 'visits';
																}

																return (
																	<React.Fragment key={i}>
																		{name}{value} {suffix}
																		<br /> 
																	</React.Fragment>
																);
															}) : null
													}
									              </TableCell>
									              <TableCell>
									              	{
									              		row.custom_metadata && 
									              		row.custom_metadata.eligible_availees && 
									              		row.custom_metadata.eligible_availees.length > 0 ? 
									              			row.custom_metadata.eligible_availees.map((availee: any, i: number) => {
									              				const found:any = AvaileeItems.find((item: any) => item.value === availee);

									              				if (found) {
									              					return (
																		<React.Fragment key={i}>
																			{found.label}
																			<br /> 
																		</React.Fragment>
									              					);
									              				}
									              				return null;
									              			}) : null
									              	}
									              </TableCell>
									            </TableRow>
				        					) : null
				        			) : 
						          	<TableRow>
							            <TableCell colSpan={4}>No additional benefits on top of Base Plan have been added.</TableCell>
						          	</TableRow>
				        	}
				        </TableBody>
				     </Table>
				</Grid>
			</Grid>
	);
};