/**
 * The global state selectors
 */

import { createSelector } from 'reselect';
import { getFilteredNodes, getSidebarList, getRows, getContractNode, getCategoryNode, getPlanTypeList } from '../CreateProposal/CreateProposalSelector';

const getExclusionList = (state: any) => {
  return state.pages.finalizeProposal.exclusionList;
};

const getProposalInfo = (state: any) => {
	return state.pages.finalizeProposal.proposalInfo;
  }
  

const getBenefitNodes = (state: any) => {
  return state.pages.finalizeProposal.benefitNodes;
};

const getSelectedPlanType = (state: any) => {
  return state.pages.finalizeProposal.selectedPlanType;
};

const getSelectedPlanPackage = (state: any) => {
  return state.pages.finalizeProposal.selectedPlanPackage;
};

const getSelectedBenefitCategory = (state: any) => {
  return state.pages.finalizeProposal.selectedBenefitCategory;
};

const getClientData = (state: any) => {
  return state.pages.finalizeProposal.clientData;
};

const selectBusinessData = createSelector(getClientData, (clientData) => {
  return clientData.businessData;
});

const selectNatureData = createSelector(getClientData, (clientData) => {
  return clientData.natureData;
});

const selectContactDetailsData = createSelector(getClientData, (clientData) => {
  return clientData.contactDetailsData;
});

const selectHMOData = createSelector(getClientData, (clientData) => {
  return clientData.HMOData;
});

const selectPrincipalData = createSelector(getClientData, (clientData) => {
  return clientData.principalData;
});

const selectContractNodes = createSelector(getBenefitNodes, (benefitNodes) => {
  return getFilteredNodes (benefitNodes, 2);
});

const selectPlanTypeNodes = createSelector(getBenefitNodes, (benefitNodes) => {
  return getFilteredNodes (benefitNodes, 3);
});

const selectPlanPackageNodes = createSelector(getBenefitNodes, (benefitNodes) => {
  return getFilteredNodes (benefitNodes, 4);
});

const selectBenefitCategoryNodes = createSelector(getBenefitNodes, (benefitNodes) => {
  return getFilteredNodes (benefitNodes, 5);
});

const selectBenefitTypeNodes = createSelector(getBenefitNodes, (benefitNodes) => {
  return getFilteredNodes (benefitNodes, 6);
});

const selectMainBenefitNodes = createSelector(getBenefitNodes, (benefitNodes) => {
  return getFilteredNodes (benefitNodes, 7);
});

const selectSubBenefitNodes = createSelector(getBenefitNodes, (benefitNodes) => {
  return getFilteredNodes (benefitNodes, 8);
});

const selectContractNode = createSelector(selectContractNodes, (contractNodes) => {
  return getContractNode(contractNodes);
});

const selectCategoryNode = createSelector(selectPlanPackageNodes, selectBenefitCategoryNodes, getSelectedPlanType, getSelectedPlanPackage, getSelectedBenefitCategory, (planPackageNodes, benefitCategoryNodes, selectedPlanType, selectedPlanPackage, selectedBenefitCategory) => {
  return getCategoryNode(planPackageNodes, benefitCategoryNodes, selectedPlanType, selectedPlanPackage, selectedBenefitCategory);
});

const selectPlanTypeList = createSelector(selectPlanTypeNodes, selectMainBenefitNodes, selectBenefitTypeNodes, selectSubBenefitNodes, (planTypeNodes, mainBenefitNodes, benefitTypeNodes, subBenefitNodes) => {
  return getPlanTypeList(planTypeNodes, mainBenefitNodes, benefitTypeNodes, subBenefitNodes);
});

const selectRows = createSelector(getExclusionList, selectBenefitCategoryNodes, selectBenefitTypeNodes, selectMainBenefitNodes, selectSubBenefitNodes, getSelectedPlanType, getSelectedPlanPackage, getSelectedBenefitCategory, (exclusionList, benefitCategoryNodes, benefitTypeNodes, mainBenefitNodes, subBenefitNodes, selectedPlanType, selectedPlanPackage, selectedBenefitCategory) => {
  return getRows(exclusionList, benefitCategoryNodes, benefitTypeNodes, mainBenefitNodes, subBenefitNodes, selectedPlanType, selectedPlanPackage, selectedBenefitCategory);
});

const selectRoomAndBoardList = createSelector(selectBenefitCategoryNodes, getSelectedPlanType, (benefitCategoryNodes, selectedPlanType) => {
  return getSidebarList (benefitCategoryNodes, selectedPlanType, 'RB');
});

const selectCoverageList = createSelector(selectBenefitCategoryNodes, getSelectedPlanType, (benefitCategoryNodes, selectedPlanType) => {
  return getSidebarList (benefitCategoryNodes, selectedPlanType, 'CV');
});

const selectRiderList = createSelector(selectBenefitCategoryNodes, getSelectedPlanType, (benefitCategoryNodes, selectedPlanType) => {
  return getSidebarList (benefitCategoryNodes, selectedPlanType, 'RI');
});

const getNewNodeCounter = (state: any) => {
  return state.pages.finalizeProposal.newNodeCounter;
}

const getEditedNodeList = (state: any) => {
  return state.pages.finalizeProposal.editedNodeList;
};

const getSelectedSummaryPackage = (state: any) => {
  return state.pages.finalizeProposal.selectedSummaryPackage;
};

const getSelectedSummaryCategory = (state: any) => {
  return state.pages.finalizeProposal.selectedSummaryCategory;
};

const getContractType = (state: any) => {
	return state.pages.finalizeProposal.contractType;
};

const selectSummaryPackages = createSelector(selectBenefitCategoryNodes, selectBenefitTypeNodes, selectMainBenefitNodes, (benefitCategoryNodes, benefitTypeNodes, mainBenefitNodes) => {
	let nodes = benefitCategoryNodes.filter((category: any) => {
		let benefits: any[] = [];

		if (["SH","DNT","EC","CC","PV", 'EM', "ADD", "SD"].includes(category.code)) {
			benefits = benefitTypeNodes.filter((benefit) => {
				return 	benefit.tree_path[2] === category.tree_path[2] &&
						benefit.tree_path[3] === category.tree_path[3] &&
						benefit.tree_path[4] === category.tree_path[4] &&
						benefit.included === true
			});

			benefits = benefits.filter((benefit) => {
				const baseNode = benefitTypeNodes.find ((baseItem) => {
		            return (
		              baseItem.name === benefit.name &&
		              baseItem.code === benefit.code &&
		              baseItem.tree_path[2] === 'BASE' &&
		              baseItem.tree_path[3] === benefit.tree_path[3] &&
		              baseItem.tree_path[4] === benefit.tree_path[4] &&
		              baseItem.tree_path[5] === benefit.tree_path[5]
		            )
		          });

				if (baseNode && !baseNode.included) {
					return true;
				} else if (baseNode === undefined) {
					return true;
				}
				return false;
			});
		} else {
			benefits = mainBenefitNodes.filter((benefit) => {
				return 	benefit.tree_path[2] === category.tree_path[2] &&
						benefit.tree_path[3] === category.tree_path[3] &&
						benefit.tree_path[4] === category.tree_path[4] &&
						benefit.included === true
			});

			benefits = benefits.filter((benefit) => {
				const baseNode = mainBenefitNodes.find ((baseItem) => {
		            return (
		              baseItem.name === benefit.name &&
		              baseItem.code === benefit.code &&
		              baseItem.tree_path[2] === 'BASE' &&
		              baseItem.tree_path[3] === benefit.tree_path[3] &&
		              baseItem.tree_path[4] === benefit.tree_path[4] &&
		              baseItem.tree_path[5] === benefit.tree_path[5] &&
		              baseItem.tree_path[6] === benefit.tree_path[6]
		            )
		          });

				if (baseNode && !baseNode.included) {
					return true;
				} else if (baseNode === undefined) {
					return true;
				}
				return false;
			});
		}	

		return (
			category.tree_path[2] !== "BASE" &&
			(["CV", "RI"].includes(category.tree_path[3]) ) &&
			benefits.length > 0
		);
	});

	// filter duplicates
	nodes = nodes.filter ((node: any, i: number)  => {
		const index = nodes.findIndex(category => 
		    category.name === node.name && 
		    category.code === node.code
		);

		if (index !== undefined && index !== i) {
			return false;
		}
		return true;
	});

	return nodes;
});

const selectSummaryBenefits = createSelector(selectPlanTypeNodes, selectBenefitTypeNodes, selectMainBenefitNodes, getSelectedSummaryPackage, getSelectedSummaryCategory, (planTypeNodes, benefitTypeNodes, mainBenefitNodes, selectedSummaryPackage, selectedSummaryCategory) => {
	let nodes = planTypeNodes.reduce ((result, item) => {
		if (item.code !== "BASE") {
			let newPlanItem = Object.assign({}, item);
			const code = newPlanItem.code;

			let benefits: any[] = [];

			if (["SH","DNT","EC","CC","PV", 'EM', "ADD", "SD"].includes(selectedSummaryCategory)) {
				benefits = benefitTypeNodes.filter((item) => {
					return 	item.tree_path[2] === code &&
							item.tree_path[3] === selectedSummaryPackage &&
							item.tree_path[4] === selectedSummaryCategory &&
							item.included === true
				});

				benefits = benefits.filter((benefit) => {
					const baseNode = benefitTypeNodes.find ((baseItem) => {
			            return (
			              baseItem.name === benefit.name &&
			              baseItem.code === benefit.code &&
			              baseItem.tree_path[2] === 'BASE' &&
			              baseItem.tree_path[3] === benefit.tree_path[3] &&
			              baseItem.tree_path[4] === benefit.tree_path[4] &&
			              baseItem.tree_path[5] === benefit.tree_path[5] 
			            )
			          });

					if (baseNode && !baseNode.included) {
						return true;
					} else if (baseNode === undefined) {
						return true;
					}
					return false;
				});

			} else {
				benefits = mainBenefitNodes.filter((item) => {
					return 	item.tree_path[2] === code &&
							item.tree_path[3] === selectedSummaryPackage &&
							item.tree_path[4] === selectedSummaryCategory &&
							item.included === true
				});

				benefits = benefits.filter((benefit) => {
					const baseNode = mainBenefitNodes.find ((baseItem) => {
			            return (
			              baseItem.name === benefit.name &&
			              baseItem.code === benefit.code &&
			              baseItem.tree_path[2] === 'BASE' &&
			              baseItem.tree_path[3] === benefit.tree_path[3] &&
			              baseItem.tree_path[4] === benefit.tree_path[4] &&
			              baseItem.tree_path[5] === benefit.tree_path[5] &&
			              baseItem.tree_path[6] === benefit.tree_path[6]
			            )
			          });

					if (baseNode && !baseNode.included) {
						return true;
					} else if (baseNode === undefined) {
						return true;
					}
					return false;
				});
			}

			newPlanItem['child_nodes'] = benefits;

			if (newPlanItem['child_nodes'].length > 0) {
				result.push(newPlanItem);
			}

		}

		return result;

	}, new Array());

	return nodes;
});

export { 
	getBenefitNodes,
	getClientData,
  selectPlanTypeNodes,
  selectSummaryPackages,
  selectSummaryBenefits,
  getSelectedSummaryPackage,
	getSelectedSummaryCategory,
	getSelectedPlanType,
	getSelectedPlanPackage,
	getSelectedBenefitCategory,
	selectPlanTypeList,
	selectRoomAndBoardList,
	selectCoverageList,
	selectRiderList,
	selectRows,
	selectBusinessData,
	selectNatureData,
	selectContactDetailsData,
	selectHMOData,
	selectPrincipalData,
	selectContractNode,
  selectCategoryNode,
  getNewNodeCounter,
  getEditedNodeList,
  getExclusionList,
  getContractType,
  getProposalInfo
};
